import { useEffect } from 'react';

import monitor from '@/utils/monitor';

interface IUseViewEventOptions {
  eventCode?: string;
  message: string;
}

/**
 * : 
 */
function useViewEvent({ eventCode, message }: IUseViewEventOptions) {
  /**
   * 
   */
  useEffect(() => {
    monitor.log({
      eventCode,
      message
    });
  }, []);
}

export default useViewEvent;
