import store from 'store';
import jsCookies from 'js-cookie';
import monitor from '@/utils/monitor';

import appConfig from '../config/app-config';

/**
 * , localStorage
 * @param key storekey
 * @param value storevalue
 */
export function set(key: string, value: string | number): void {
  try {
    store.set(key, value);
  } catch (error) {
    monitor.error({
      message: `storekey=>${key},value=${value}error=>${JSON.stringify(error)}`
    });
  }
}

/**
 * 
 * @param key storekey
 */
export function get(key: string): string | number {
  return store.get(key);
}

/**
 * 
 * @param key key
 */
export function remove(key: string): void {
  store.remove(key);
}

/**
 * token
 * @param userId id
 * @param token token
 */
export function setToken(userId: string, token: string): void {
  const { userIdKey, userTokenKey, expiresDays, domain } = appConfig.cookie;

  set(userIdKey, userId);
  set(userTokenKey, token);

  jsCookies.set(userIdKey, userId, {
    expires: expiresDays,
    domain
  });

  jsCookies.set(userTokenKey, token, {
    expires: expiresDays,
    domain
  });
}

/**
 * token
 */
export function getToken(): string | undefined {
  const { userIdKey, userTokenKey, expiresDays, domain } = appConfig.cookie;
  const userId: string = store.get(userIdKey);
  const userStoreToken: string = store.get(userTokenKey);
  const userCookieToken: string | undefined = jsCookies.get(userTokenKey);

  // token
  const userToken: string | undefined = userStoreToken || userCookieToken;

  // store 
  if (userToken && !userStoreToken) {
    set(userIdKey, userId);
    set(userTokenKey, userToken);
  }

  // cookie 
  if (userToken && !userCookieToken) {
    jsCookies.set(userIdKey, userId, {
      expires: expiresDays,
      domain
    });

    jsCookies.set(userTokenKey, userToken, {
      expires: expiresDays,
      domain
    });
  }

  return userToken;
}

/**
 * token
 * PS: urlcode, Token
 */
export function getWechatToken(): string {
  const { wechatTokenKey, expiresDays, domain } = appConfig.cookie;

  const storeToken: string = store.get(wechatTokenKey);
  const cookieToken: string = jsCookies.get(wechatTokenKey) || '';

  //  token
  const wechatToken: string = storeToken || cookieToken;

  // store 
  if (wechatToken && !storeToken) {
    set(wechatTokenKey, wechatToken);
  }

  // cookie 
  if (wechatToken && !cookieToken) {
    // cookie, 
    jsCookies.set(wechatTokenKey, wechatToken, {
      expires: expiresDays,
      domain
    });
  }

  return wechatToken;
}

/**
 * tokentoken
 */
export function clearToken(): void {
  const { userIdKey, userTokenKey, domain } = appConfig.cookie;
  store.remove(userIdKey);
  store.remove(userTokenKey);

  jsCookies.remove(userIdKey);
  jsCookies.remove(userTokenKey);

  jsCookies.remove(userIdKey, { domain });
  jsCookies.remove(userTokenKey, { domain });

  jsCookies.remove(userIdKey, { domain: 'mmears.com' });
  jsCookies.remove(userTokenKey, { domain: 'mmears.com' });

  jsCookies.remove(userIdKey, { domain: '.mmears.com' });
  jsCookies.remove(userTokenKey, { domain: '.mmears.com' });
}

/**
 * userId
 */
export const getUserId = () => {
  const { userIdKey, expiresDays, domain } = appConfig.cookie;

  const storeUserId = store.get(userIdKey);
  const cookieUserId = jsCookies.get(userIdKey);

  const userId = storeUserId || cookieUserId;

  // store 
  if (userId && !storeUserId) {
    set(userIdKey, userId);
  }

  // cookie 
  if (userId && !cookieUserId) {
    jsCookies.set(userIdKey, userId, {
      expires: expiresDays,
      domain
    });
  }

  return userId;
};
