import React, { FC, useState, useCallback, useEffect, useReducer } from 'react';
import ReactDOM from 'react-dom';
import classNames from 'classnames';

import useAliPay from './hooks/useAliPay';
import useUrlPay from './hooks/useUrlPay';
import useUnionPay from './hooks/useUnionPay';
import useQrCodePay from './hooks/useQrCodePay';
import useCmblifePay from './hooks/useCmblifePay';

import PaymentMethodItem from './payment-method-item';
import * as productService from '@/service/product-service';
import { useStopBodyScroll } from '@/components/hooks';
import { ERROR_CODE, PAYMENT_NAME } from '@/constants';
import { IPaymentInfo, IPaymentMethod, IPaymentStage, ISelectMethod } from '@/types/payment';
import message from '@/utils/message';
import { tools } from '@/utils';
import styles from './payment.less';
import msToast from '@/components/modal/msToast';
import monitor from '@/utils/monitor';
import * as localStore from '@/utils/local-store';

export interface IPaymentMethodProps {
  shouldPrice: string;
  versionId?: number;
  couponId?: string;
  paymentMethod: IPaymentMethod;
  onTipMessage: (msg: string, type?: 'reload' | 'redirect') => void; // 
  onClose: () => void;
}

interface IState {
  selectedName: string; // 
  stageCount: string; // 
  selectMethod: ISelectMethod | null; // 
  paymentInfo: IPaymentInfo | null; //  
}

interface IAction {
  type: 'update';
  payload: Partial<IState>;
}

/**
 * 
 * @param paymentMethod 
 */
function init(paymentMethod: IPaymentMethod) {
  const initState = {
    selectedName: '',
    stageCount: '',
    selectMethod: null,
    paymentInfo: null
  };

  if (paymentMethod.paymentList.length > 0) {
    // 
    const method = paymentMethod.paymentList[0];
    initState.selectedName = method.paymentName;

    // , 
    if (Array.isArray(method.orderStages) && method.orderStages.length > 0) {
      const stageInfo: IPaymentStage = method.orderStages[0];
      initState.stageCount = stageInfo.stageCount;
    }
  }

  return initState;
}

function reducer(state: IState, action: IAction): IState {
  switch (action.type) {
    case 'update':
      return {
        ...state,
        ...action.payload
      };
      break;
    default:
      return {
        ...state
      };
      break;
  }
}

/**
 * 
 */
const PaymentMethod: FC<IPaymentMethodProps> = ({
  shouldPrice,
  versionId,
  couponId,
  paymentMethod,
  onTipMessage,
  onClose
}) => {
  /**
   * 
   */
  const [container] = useState(() => {
    return document.createElement('div');
  });

  // 
  const [animationClass, setAnimationClass] = useState('');
  const [state, dispatch] = useReducer(reducer, paymentMethod, init);
  const { selectMethod, paymentInfo } = state;

  /**
   * 
   */
  useStopBodyScroll();

  /**
   * H5, : H5, H5
   */
  useAliPay(paymentInfo as IPaymentInfo, selectMethod);

  /**
   *   app
   */
  useCmblifePay(paymentInfo as IPaymentInfo, selectMethod);

  /**
   * url, :   
   */
  useUrlPay(paymentInfo as IPaymentInfo, selectMethod);

  /**
   * 
   */
  useUnionPay(selectMethod);

  /**
   *  PC, , :  | 
   */
  useQrCodePay(selectMethod);

  /**
   *  |  container
   */
  useEffect(() => {
    document.body.appendChild(container);

    return () => {
      document.body.removeChild(container);
    };
  }, []);

  useEffect(() => {
    const timeout = 200;
    const timer = setTimeout(() => {
      // 
      setAnimationClass(styles.boxUp);
    }, timeout);

    return () => {
      clearTimeout(timer);
    };
  }, []);

  /**
   * 
   */
  const handleClose = () => {
    onClose && onClose();
  };

  /**
   * 
   */
  const handleClick = useCallback(
    tools.throttle(async () => {
      if (!state.selectedName) {
        message.error('');
        return;
      }

      const selectPayment = paymentMethod.paymentList.find(
        (item) => item.paymentName === state.selectedName
      );
      if (!selectPayment) {
        message.error('');
        return;
      }

      // id
      const productId = paymentMethod.productId;
      // 
      const paymentName = selectPayment.paymentName;
      // 
      const paymentText = selectPayment.paymentText;
      // 
      const orderNo = paymentMethod.orderNo;

      // 
      const selectMethod: ISelectMethod = {
        productId,
        orderNo,
        paymentName,
        stageCount: state.stageCount
      };

      /**
       * 
       */
      let paymentInfo: IPaymentInfo | null = null;

      try {
        //  loading
        message.showLoading();

        // 1. , 
        if (!orderNo) {
          const orderInfo = await productService.createOrder({
            productId,
            versionId: versionId as number,
            couponId: couponId as string,
            paymentName
          });

          // 
          selectMethod.orderNo = orderInfo.orderNo;
        }

        // 2. 
        if (
          selectMethod.paymentName === PAYMENT_NAME.AlipayWap || // wap
          selectMethod.paymentName === PAYMENT_NAME.AlipayHB || // 
          selectMethod.paymentName === PAYMENT_NAME.CmblifeFQ || // 
          selectMethod.paymentName === PAYMENT_NAME.CCB_BLANK_FQ || // 
          selectMethod.paymentName === PAYMENT_NAME.GNETPG_FQ // 
        ) {
          paymentInfo = await productService.getPaymentInfo(selectMethod);
        }

        //  loading
        message.closeLoading();
      } catch (error) {
        //  loading
        message.closeLoading();

        // 
        monitor.error({
          message: `: ${paymentText}, , , ${JSON.stringify(error)}`
        });
        const { code, msg } = error;
        switch (code) {
          case ERROR_CODE.LOGIN_REQUIRED:
            // message.error('', () => {
            //   window.location.replace(tools.getLoginUrl());
            // });
            msToast.modal({
              content: '',
              closeVisible: false,
              okText: '',
              onOk: () => {
                window.location.replace(tools.getLoginUrl());
              }
            });
            break;
          case ERROR_CODE.BIZ_ORDER_PRODUCT_VERSION_CHANGE: // 
          case ERROR_CODE.BIZ_COUPON_VERSION_MISMATCHING: // 
          case ERROR_CODE.BIZ_ORDER_NOT_EXIST: // 
          case ERROR_CODE.BIZ_ORDER_IS_PAID: // 
          case ERROR_CODE.BIZ_ORDER_IS_PAID_SUB: // 
          case ERROR_CODE.BIZ_ORDER_IS_REFUND: // 
          case ERROR_CODE.BIZ_ORDER_EXPIRYTIME_OVER: // 
          case ERROR_CODE.BIZ_ORDER_IS_CLOSE: // 
          case ERROR_CODE.BIZ_ORDER_IS_SUB: // 
          case ERROR_CODE.BIZ_ORDER_NO_USER_ID_NOT_MATCH: // ID
          case ERROR_CODE.BIZ_ORDER_STATUS_IS_ERROR: // 
            onTipMessage && onTipMessage(msg, 'reload');
            break;
          default:
            onTipMessage && onTipMessage(msg);
            break;
        }

        return;
      }

      // 
      monitor.log({
        eventCode: tools.getEventCodeByPath(),
        message: `: ${paymentText}, `
      });

      //  PAYMENT_PAGE
      localStore.set('PAYMENT_PAGE', tools.getPaymentPage());

      //   
      dispatch({
        type: 'update',
        payload: {
          paymentInfo,
          selectMethod
        }
      });
    }, 1000),
    [state.selectedName, state.stageCount]
  );

  /**
   * 
   */
  const handleSelect = (selectedName: string, stageCount: string) => {
    // 
    dispatch({
      type: 'update',
      payload: {
        selectedName,
        stageCount: stageCount || ''
      }
    });
  };

  /**
   * 
   */
  const methodList = paymentMethod.paymentList.map((item) => {
    const selected = state.selectedName === item.paymentName;
    return (
      <PaymentMethodItem
        key={item.paymentName}
        item={item}
        selected={selected}
        stageCount={state.stageCount}
        onSelected={handleSelect}
      />
    );
  });

  /**
   * 
   */
  const renderContent = () => {
    return (
      <div className={styles.mask}>
        <div className={classNames(styles.box, animationClass)}>
          <header>
            
            <span onClick={handleClose}></span>
          </header>
          <div className={styles.content}>{methodList}</div>
          <footer>
            <div onClick={handleClick}>
              <i></i>
              {shouldPrice}
            </div>
          </footer>
        </div>
      </div>
    );
  };

  return ReactDOM.createPortal(renderContent(), container as Element);
};

export default PaymentMethod;
