import { useEffect } from 'react';

import { ISelectMethod } from '@/types/payment';
import { PAYMENT_NAME } from '@/constants';
import { tools } from '@/utils';

/**
 * PC, , :  | 
 * : 
 * @param selectMethod 
 */
const useQrCodePay = (selectMethod: ISelectMethod | null) => {
  useEffect(() => {
    if (selectMethod === null) {
      return;
    }
    //  || 
    if (
      selectMethod.paymentName === PAYMENT_NAME.WechatQR ||
      selectMethod.paymentName === PAYMENT_NAME.AlipayQR
    ) {
      const url =
        `${tools.getFullPath('/pay/qrcode')}` +
        `?productId=${selectMethod.productId}` +
        `&orderNo=${selectMethod.orderNo || ''}` +
        `&method=${selectMethod.paymentName.toLowerCase()}`;

      window.location.href = url;
    }
  }, [selectMethod]);
};

export default useQrCodePay;
