import React, { FC, useContext } from 'react';
import classNames from 'classnames';

import { tools } from '@/utils';
import { ORDER_STATUS } from '@/constants/order';
import PaymentButton from '../payment/payment-button';
import CountDown from './count-down';
import { OrderContext } from './model';

import styles from './order-payment.less';

interface IOrderPaymentProps {
  isFixed?: boolean;
  status: ORDER_STATUS; // 
  endTime: string; // 
  currentTime: string; // 
  orderNo: string; // 
  shouldPrice: number | string; // 
  onCountDownFinish: () => void; // 
}
/**
 * 
 */
const OrderPayment: FC<IOrderPaymentProps> = ({
  isFixed = true,
  status,
  endTime,
  currentTime,
  orderNo,
  shouldPrice,
  onCountDownFinish
}) => {
  const dispatch = useContext(OrderContext);
  if (status !== ORDER_STATUS.UNPAID) {
    return null;
  }

  // 
  const expires = endTime < currentTime;
  const clsPayTime = classNames(styles.payTime, {
    [styles['disabled-payTime']]: expires
  });

  /**
   * , , 
   */
  let countDownElement = null;
  if (endTime) {
    countDownElement = (
      <>
        <div className={styles.payTip}></div>
        <CountDown
          endTime={endTime}
          currentTime={currentTime}
          onFinish={onCountDownFinish}
          className={clsPayTime}
        />
      </>
    );
  }

  // Fixed
  const clsPaybar = isFixed ? styles.payBarFixed : styles.payBar;
  const clsPay = classNames(styles.pay, { [styles['disabled-pay']]: expires });
  return (
    <div className={clsPaybar}>
      <div className={styles.payContent}>
        {countDownElement}
        <div className={clsPay}>
          <PaymentButton
            productId={''}
            disabled={expires}
            orderNo={orderNo}
            shouldPrice={tools.convertPennyToDollar(shouldPrice) as string}
            onReloadData={dispatch}
          />
        </div>
      </div>
    </div>
  );
};

export default OrderPayment;
