import { useEffect } from 'react';

import { ISelectMethod } from '@/types/payment';
import { PAYMENT_NAME } from '@/constants';
import { tools } from '@/utils';

/**
 * 
 * : 
 * @param selectMethod 
 */
const useUnionPay = (selectMethod: ISelectMethod | null) => {
  useEffect(() => {
    if (selectMethod === null) {
      return;
    }

    // 
    if (
      selectMethod.paymentName === PAYMENT_NAME.UNIONPAY_BOC_FQ || // xxxxxxxxx 
      selectMethod.paymentName === PAYMENT_NAME.UNIONPAY_ABOC_FQ || // 
      selectMethod.paymentName === PAYMENT_NAME.UNIONPAY_HXB_FQ || // xxxxxxxxx 
      selectMethod.paymentName === PAYMENT_NAME.UNIONPAY_SPDB_FQ || // 
      selectMethod.paymentName === PAYMENT_NAME.UNIONPAY_CMBC_FQ // 
    ) {
      const queryString =
        `orderNo=${selectMethod.orderNo}` +
        `&method=${selectMethod.paymentName.toLowerCase()}` +
        `&stageCount=${selectMethod.stageCount}`;

      const url = tools.getFullPath(`/pay/unionpay?${queryString}`);
      console.log(', url', url);
      window.location.href = url;
    }
  }, [selectMethod]);
};

export default useUnionPay;
