import React, { FC, useCallback, useEffect, useReducer } from 'react';

import PaymentMethod from '../payment/payment-method';
import useWechatPay from '../payment/hooks/useWechatPay';

import { Modal } from '@/components/modal/modal';
import { tools } from '@/utils';
import monitor from '@/utils/monitor';
import message from '@/utils/message';
import * as localStore from '@/utils/local-store';
import { IWechatPayOptions } from '@/types/wechat';
import { IPaymentMethod, ISelectMethod } from '@/types/payment';
import * as productService from '@/service/product-service';
import { ERROR_CODE, PAYMENT_NAME } from '@/constants';
import msToast from '@/components/modal/msToast';

/**
 * 
 */
const defaultErrorMsg = ', ';

interface IPaymentButtonProps {
  /**
   * id
   */
  productId: string;
  /**
   * , , ; , 
   */
  versionId?: number;
  /**
   * Id, , id; , id
   */
  couponId?: string;
  /**
   * , , ; , 
   */
  orderNo?: string;
  /**
   * , :
   */
  shouldPrice?: string;
  /**
   * 
   */
  disabled?: boolean;
  /**
   * 
   *   |  |  |  | , 
   */
  onReloadData?: () => void;

  /**
   * ,  ``
   */
  buttonText?: string;
}

interface IState {
  modalKey: string;
  /**
   * 
   */
  paymentMethod?: IPaymentMethod;
  /**
   * 
   */
  wechatPayOptions?: IWechatPayOptions;
  /**
   * 
   */
  tipMsg: string;
  /**
   * ,  | 
   */
  tipType?: 'reload' | 'redirect';
}

enum IActionType {
  paymentMethod = 'paymentMethod', // 
  close = 'close', // 
  wechatPay = 'wechatPay', // 
  tipMessage = 'tipMessage' // 
}

interface IAction {
  type: IActionType;
  payload?: IPaymentMethod | IWechatPayOptions | { msg: string; type?: 'reload' | 'redirect' };
}

/**
 * 
 */
const initState = {
  modalKey: '',
  paymentMethod: undefined,
  wechatPayOptions: undefined,
  tipMsg: ''
};

/**
 * reducer
 */
function reducer(state: IState, action: IAction): IState {
  switch (action.type) {
    case IActionType.paymentMethod: // 
      return {
        ...state,
        modalKey: 'paymentMethod',
        paymentMethod: action.payload as IPaymentMethod
      };
    case IActionType.close: // 
      return {
        ...state,
        modalKey: '',
        paymentMethod: undefined
      };
    case IActionType.wechatPay: // 
      return {
        ...state,
        wechatPayOptions: action.payload as IWechatPayOptions
      };
      break;
    case IActionType.tipMessage:
      return {
        ...state,
        modalKey: IActionType.tipMessage,
        tipMsg: (action.payload as any).msg as string,
        tipType: (action.payload as any).type
      };
      break;
    default:
      return {
        ...state
      };
  }
}

// 
let payFlag = false;
//  payFlag
let payFlagTimer: NodeJS.Timeout;
// 
const timeout = 500;

/**
 * , 
 */
const PaymentButton: FC<IPaymentButtonProps> = (props) => {
  const {
    productId,
    versionId,
    couponId,
    orderNo,
    shouldPrice,
    disabled,
    onReloadData,
    buttonText = ''
  } = props;

  // 
  const [state, dispatch] = useReducer(reducer, initState);

  /**
   * 
   */
  useWechatPay(state.wechatPayOptions, onReloadData);

  /**
   * 
   */
  useEffect(() => {
    return () => {
      if (payFlagTimer) {
        clearTimeout(payFlagTimer);
      }
    };
  }, []);

  /**
   * 
   */
  const handleTipMessage = useCallback((msg: string, type?: 'reload' | 'redirect') => {
    dispatch({
      type: IActionType.tipMessage,
      payload: {
        msg,
        type
      }
    });
  }, []);

  /**
   * 
   */
  const handleClose = useCallback(() => {
    // 
    dispatch({
      type: IActionType.close
    });

    // 
    onReloadData && onReloadData();
  }, []);

  /**
   * 
   * @param error 
   */
  const handleError = (error: any) => {
    // loading
    // message.closeLoading();
    console.error('handleWechatPay', error);
    const { code, msg } = error;
    switch (code) {
      case ERROR_CODE.LOGIN_REQUIRED:
        // message.error('', () => {
        //   window.location.replace(tools.getLoginUrl());
        // });
        msToast.modal({
          content: '',
          closeVisible: false,
          okText: '',
          onOk: () => {
            window.location.replace(tools.getLoginUrl());
          }
        });
        break;
      case ERROR_CODE.BIZ_ORDER_PRODUCT_VERSION_CHANGE: // 
      case ERROR_CODE.BIZ_COUPON_VERSION_MISMATCHING: // 
      case ERROR_CODE.BIZ_ORDER_NOT_EXIST: // 
      case ERROR_CODE.BIZ_ORDER_IS_PAID: // 
      case ERROR_CODE.BIZ_ORDER_IS_PAID_SUB: // 
      case ERROR_CODE.BIZ_ORDER_IS_REFUND: // 
      case ERROR_CODE.BIZ_ORDER_EXPIRYTIME_OVER: // 
      case ERROR_CODE.BIZ_ORDER_IS_CLOSE: // 
      case ERROR_CODE.BIZ_ORDER_IS_SUB: // 
      case ERROR_CODE.BIZ_ORDER_NO_USER_ID_NOT_MATCH: // ID
      case ERROR_CODE.BIZ_ORDER_STATUS_IS_ERROR: // 
        handleTipMessage(msg, 'reload');
        break;
      default:
        handleTipMessage(msg);
        break;
    }
  };

  /**
   * 
   * : productId, versionId, couponId, orderNo
   */
  const handleWechatPay = useCallback(async () => {
    // , 
    if (payFlag) {
      return;
    }

    payFlag = true;

    try {
      message.showLoading();

      // 
      const paymentName = PAYMENT_NAME.WechatMP;

      // , 
      const selectMethod: ISelectMethod = {
        productId,
        orderNo,
        versionId,
        paymentName
      };

      // 1. , 
      if (!selectMethod.orderNo) {
        const orderInfo = await productService.createOrder({
          productId,
          versionId: versionId as number,
          couponId: couponId as string,
          paymentName
        });
        // 
        selectMethod.orderNo = orderInfo.orderNo;
      }

      // 2. 
      const wechatPayOptions = await productService.getWechatPaymentInfo(selectMethod);

      // 
      monitor.log({
        eventCode: tools.getEventCodeByPath(),
        message: ` '${buttonText}', `
      });

      //  PAYMENT_PAGE
      localStore.set('PAYMENT_PAGE', tools.getPaymentPage());

      // loading
      message.closeLoading();

      // 3. 
      dispatch({
        type: IActionType.wechatPay,
        payload: wechatPayOptions
      });
    } catch (error) {
      // loading
      message.closeLoading();
      // 
      monitor.error({
        message: ` '${buttonText}', , , ${JSON.stringify(error)}`
      });
      handleError(error);
    } finally {
      // // loading
      // message.closeLoading();
      // 
      payFlagTimer = setTimeout(() => {
        payFlag = false;
      }, timeout);
    }
  }, [productId, versionId, couponId, orderNo]);

  /**
   * 
   * @param productId Id, 
   * @param orderNo , 
   * @param versionId ,  
   * @param dispatch dispatch
   */
  const handleShowPaymentMethod = useCallback(async () => {
    // , 
    if (payFlag) {
      return;
    }

    payFlag = true;

    let paymentMethod: IPaymentMethod;

    try {
      message.showLoading();

      // Id, ? 
      paymentMethod = await productService.getPaymentMethod({
        productId,
        orderNo,
        couponId
      });

      // 3. 
      dispatch({
        type: IActionType.paymentMethod,
        payload: paymentMethod
      });

      // loading
      message.closeLoading();

      // 
      monitor.log({
        message: ` '${buttonText}', `
      });
    } catch (error) {
      // loading
      message.closeLoading();

      // 
      monitor.error({
        message: ` '${buttonText}', , , ${JSON.stringify(error)}`
      });

      handleError(error);
    } finally {
      // // loading
      // message.closeLoading();
      // 
      payFlagTimer = setTimeout(() => {
        payFlag = false;
      }, timeout);
    }
  }, [productId, couponId, orderNo]);

  // 
  let modalElement = null;
  switch (state.modalKey) {
    case IActionType.paymentMethod: // 
      modalElement = (
        <PaymentMethod
          shouldPrice={shouldPrice as string}
          versionId={versionId}
          couponId={couponId}
          paymentMethod={state.paymentMethod as IPaymentMethod}
          onTipMessage={handleTipMessage}
          onClose={handleClose}
        />
      );
      break;
    case IActionType.tipMessage: // ,    
      modalElement = (
        <Modal
          visible={true}
          closeVisible={false}
          onOk={() => {
            // 
            dispatch({
              type: IActionType.close
            });

            switch (state.tipType) {
              case 'redirect': // 
                //  
                if (/\/order$/.test(location.pathname)) {
                  onReloadData && onReloadData();
                  return;
                }
                // 
                window.location.href = tools.getFullPath('/order');
                break;
              case 'reload': // 
                // 
                onReloadData && onReloadData();
                break;
            }
          }}
        >
          {state.tipMsg || defaultErrorMsg}
        </Modal>
      );
      break;
  }

  let handleClick;
  if (disabled) {
    handleClick = tools.noop;
  } else {
    if (tools.isWeChat()) {
      handleClick = handleWechatPay;
    } else {
      handleClick = handleShowPaymentMethod;
    }
  }

  return (
    <>
      <div onClick={handleClick}>{buttonText}</div>
      {modalElement}
    </>
  );
};

export default PaymentButton;
