import appConfig from '@/config/app-config';
import { EVENT_CODE, PAYMENT_PAGE } from '@/constants';
import * as localStore from '@/utils/local-store';

/**
 * 
 * @param value 
 * @returns boolean
 */
export function isPlainObject(value: any): boolean {
  return Object.prototype.toString.call(value) === '[object Object]';
}

/**
 * 
 * @param userAgent userAgent
 * @returns boolean
 */
export function isWeChat(userAgent?: string): boolean {
  let agent = userAgent ? userAgent : window.navigator.userAgent;
  return /micromessenger/i.test(agent);
}

/**
 * wap
 */
export const isWap = () => {
  return /iphone|android|mobile|windows ce|miui|ipad|ipod|vivobrowser/i.test(navigator.userAgent);
};

/**
 * IOS
 * @param userAgent ,UA
 * @returns boolean
 */
export function isIOS(userAgent?: string): boolean {
  let agent = userAgent ? userAgent : window.navigator.userAgent;
  return !!agent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
}

/**
 * 
 * @param userAgent ,UA
 * @returns boolean
 */
export function isAndroid(userAgent?: string): boolean {
  let agent = userAgent ? userAgent : window.navigator.userAgent;
  return agent.indexOf('Android') > -1 || agent.indexOf('Adr') > -1;
}

/**
 * 
 * @param account 
 */
export function encryptAccount(account: string): string {
  const phoneReg = /^1[0-9]\d{9}$/;
  if (account) {
    if (phoneReg.test(account)) {
      return account.replace(/^(\d{3})\d{4}(\d{4})$/, '$1****$2');
    }
  }
  return account;
}

/**
 * 60
 */
const TIME_UNIT = 60;

/**
 * 
 * @param diffTime , :
 */
export const formatDiffTime = (diffTime: number) => {
  const hour = Math.floor(diffTime / (TIME_UNIT * TIME_UNIT));
  let leaveSecond = diffTime - hour * TIME_UNIT * TIME_UNIT;
  const minute = Math.floor(leaveSecond / TIME_UNIT);
  leaveSecond = leaveSecond - minute * TIME_UNIT;

  const hourVal = hour > 0 ? `${hour}` : '';
  const minuteVal = minute > 0 ? `${minute}` : '';
  const secondVal = leaveSecond >= 10 ? `${leaveSecond}` : `0${leaveSecond}`;

  return hourVal + minuteVal + secondVal;
};

/**
 * 
 * @param money 
 */
export const formatMoney = (money: number | string): number | string => {
  if (money === 'NaN') {
    return 0;
  }

  let num = (money || 0).toString();
  let result = '';
  let cent = '';
  let place = -3;
  if (/\.\d{2}/.test(num)) {
    cent = num.substr(num.indexOf('.'));
    num = num.substr(0, num.indexOf('.'));
    if (cent === '.00') {
      cent = '';
    }
  }
  while (num.length > 3 && !/^-\d{3}$/.test(num)) {
    result = ',' + num.slice(place) + result;
    num = num.slice(0, num.length - 3);
  }

  if (num) {
    result = num + result;
  }
  result += cent;
  return result;
};

/**
 * 
 * 
 * @param penny 
 */
export const convertPennyToDollar = (penny: number | string = 0): number | string => {
  if (penny === 0) {
    return penny.toFixed(2);
  }
  if (typeof penny !== 'number' || !penny) {
    return penny;
  }
  let dollar: number | string = (penny * 0.01).toFixed(2);

  const dollars = dollar.split('.');
  const dollarInt = formatMoney(dollars[0]);
  const dollarFloat = dollars[1];

  dollar = `${dollarInt}.${dollarFloat}`;

  return dollar;
};

/**
 * 
 * @param action 
 * @param delay , 
 */
export const throttle = function(action: () => void, delay: number) {
  let last = 0;
  return (...args: []) => {
    let current = Number(new Date());
    if (current - last > delay) {
      action.apply(null, args);
      last = current;
    }
  };
};

/**
 * 
 * @param action 
 * @param delay , 
 */
export const debounce = function(action: () => void, delay = 300) {
  let timer: NodeJS.Timeout;
  return (...args: []) => {
    clearTimeout(timer);
    timer = setTimeout(() => {
      action.apply(null, args);
    }, delay);
  };
};

/**
 * , urlcallback
 */
export const getLoginUrl = () => {
  const redirectUrl = `${appConfig.resources.loginUrl}?callback=${encodeURIComponent(
    window.location.href
  )}`;
  return redirectUrl;
};

const MONEY_CONVERT_UNIT = 0.01;

/**
 * 
 * @param penny 
 */
export const convertPenny = (penny: number): string => {
  const money = penny * MONEY_CONVERT_UNIT;
  return money.toFixed(2);
};

/**
 * promise
 * @param promise 
 */
export const isPromise = (promise: any) => {
  return !!promise && typeof promise.then === 'function';
};

/**
 *  
 * @param path 
 */
export const getFullPath = (path: string) => {
  let fullPath = appConfig.resources.websiteDomain + appConfig.basename + path;
  return fullPath;
};

/*
 * 
 * @param path 
 */
export const getPathWithBasename = (path: string) => {
  let basePath = appConfig.basename + path;
  return basePath;
};

/**
 * 
 */
export const noop = () => {
  console.log('');
};

/**
 * url
 */
export function getEventCodeByPath(): string {
  const url = window.location.href;
  if (/\/product\/\d+/i.test(url)) {
    return EVENT_CODE.CLICK_PAYMENT__PRODUCT_DETAIL;
  } else if (/\/order\/\d+/i.test(url)) {
    return EVENT_CODE.CLICK_PAYMENT__ORDER_DETAIL;
  } else {
    return EVENT_CODE.CLICK_PAYMENT__ORDER_LIST;
  }
}

/**
 * url
 */
export function getPaymentPage(): string {
  const url = window.location.href;
  if (/\/product\/\d+/i.test(url)) {
    return PAYMENT_PAGE.PRODUCT_DETAIL;
  } else if (/\/order\/\d+/i.test(url)) {
    return PAYMENT_PAGE.ORDER_DETAIL;
  } else {
    return PAYMENT_PAGE.ORDER_LIST;
  }
}

/**
 *  , 
 * @param success 
 */
export function getEventCodeByPaymentPage(success: boolean): string {
  const paymentPage = localStore.get('PAYMENT_PAGE');
  let eventCode = '';

  switch (paymentPage) {
    case PAYMENT_PAGE.PRODUCT_DETAIL:
      eventCode = success
        ? EVENT_CODE.PAYMENT_SUCCESS__PRODUCT_DETAIL
        : EVENT_CODE.PAYMENT_FAIL__PRODUCT_DETAIL;
      break;
    case PAYMENT_PAGE.ORDER_DETAIL:
      eventCode = success
        ? EVENT_CODE.PAYMENT_SUCCESS__ORDER_DETAIL
        : EVENT_CODE.PAYMENT_FAIL__ORDER_DETAIL;
      break;
    case PAYMENT_PAGE.ORDER_LIST:
      eventCode = success
        ? EVENT_CODE.PAYMENT_SUCCESS__ORDER_LIST
        : EVENT_CODE.PAYMENT_FAIL__ORDER_LIST;
      break;
  }

  return eventCode;
}
