import React, { FC, useState, useCallback, useContext, useRef } from 'react';
import { Button } from 'antd-mobile';
import useSimpleCountDown from '@/components/hooks/useSimpleCountDown';
import { ERROR_CODE, SMS_CODE } from '@/constants';
import { postUnionSmsCode } from '@/service/product-service';
import { Context, IUnionPayState } from './model';
import message from '@/utils/message';
import { tools } from '@/utils';
import msToast from '@/components/modal/msToast';
import monitor from '@/utils/monitor';

interface ISmsCodeButtonProps {
  className?: string;
  time?: number;
}

/**
 *  
 * @param className 
 * @param time 
 */
const SmsCodeButton: FC<ISmsCodeButtonProps> = ({ className, time = SMS_CODE.TIME }) => {
  const { orderNo, paymentName, bankNo, phoneNo }: IUnionPayState = useContext(Context);

  //   disabled
  const [disabled, updateDisabled] = useState<boolean>(false);
  //   loading
  const [loading, updateLoading] = useState<boolean>(false);

  /**
   * 
   */
  const ref = useRef<number>(0);

  /**
   * 
   */
  const handleCountdownFinish = useCallback(() => {
    updateDisabled(false);
  }, []);

  /**
   * 
   * countdownTime 
   * updateCountdownActive  
   */
  const [countdownTime, updateCountdownActive] = useSimpleCountDown(time, handleCountdownFinish);

  /**
   * 
   */
  const handleBtnClick = useCallback(async () => {
    try {
      updateDisabled(true);
      updateLoading(true);
      if (!orderNo || !bankNo || !paymentName || !phoneNo) {
        monitor.log({
          message: `[API] => ${JSON.stringify({
            orderNo,
            bankNo,
            paymentName,
            phoneNo
          })}`
        });
        message.error('');
        return;
      }
      const bankStr = bankNo.replace(/\s+/g, '');
      monitor.log({
        message: `[API] => ${JSON.stringify({
          orderNo,
          bankNo: bankStr,
          paymentName,
          phoneNo
        })}`
      });
      await postUnionSmsCode({ orderNo, bankNo: bankStr, phoneNo, paymentName });
      // 
      ref.current = ref.current + 1;
      monitor.log({
        message: `[API]${ref.current}`
      });
      // 
      updateCountdownActive(true);
    } catch (error) {
      monitor.error({
        message: `[API] => ${JSON.stringify(error)}`
      });
      updateDisabled(false);
      const { code, msg } = error;
      switch (code) {
        case ERROR_CODE.LOGIN_REQUIRED:
          // message.error('', () => {
          //   window.location.replace(tools.getLoginUrl());
          // });
          msToast.modal({
            content: '',
            closeVisible: false,
            okText: '',
            onOk: () => {
              window.location.replace(tools.getLoginUrl());
            }
          });
          break;
        default:
          msToast.modal({
            content: msg || '',
            closeVisible: false,
            okText: ''
          });
          break;
      }
    } finally {
      updateLoading(false);
    }
  }, []);

  let text = '';
  if (countdownTime >= 0 && countdownTime <= time && disabled) {
    text = `${countdownTime}s`;
  }
  if (loading) {
    text = '...';
  }

  return (
    <Button type="primary" className={className} disabled={disabled} onClick={handleBtnClick}>
      {text}
    </Button>
  );
};

export default SmsCodeButton;
