import { useEffect, useState } from 'react';
import dayjs from 'dayjs';

import { useInterval, useVisibilityChange } from '../../components/hooks';

/**
 * 
 */
const TIME_OVER = 0;

/**
 * 
 */
function getDiffTime(currentTime: string | dayjs.Dayjs, endTime: string | dayjs.Dayjs) {
  const current = dayjs(currentTime);
  const end = dayjs(endTime);

  // 1.  0
  if (isNaN(current.valueOf()) || isNaN(end.valueOf())) {
    return 0;
  }
  // 2. 
  // console.log('useCountDown >>> , ...', current, end, current.valueOf(), end.valueOf());
  if (current.valueOf() >= end.valueOf()) {
    return 0;
  }
  // 3. , 
  const diff = end.diff(current, 'second');
  return diff;
}

/**
 * 
 * @param currentTime , 
 * @param endTime 
 * @param onFinish 
 */
function useCountDown(currentTime: string, endTime: string, onFinish?: () => void): number {
  /**
   * 
   */
  const [diffTime, setDiffTime] = useState<number>(() => {
    return getDiffTime(currentTime, endTime);
  });

  /**
   * , 
   *   , 
   */
  const [active, setActive] = useState<boolean>(() => {
    const diff = getDiffTime(currentTime, endTime);
    return diff > 0 ? true : false;
  });

  /**
   * , currentTime  endTime 
   */
  const [isInit, setIsInit] = useState<boolean>(true);

  // 
  let outTime = dayjs();

  /**
   * ,   
   */
  const setInTime = () => {
    const inOutdiff = getDiffTime(outTime, dayjs());
    let diff = diffTime - inOutdiff;
    if (diff < 0) {
      diff = 0;
    }

    setDiffTime(diff);
  };

  /**
   * 
   */
  useVisibilityChange(undefined, setInTime);

  /**
   * ,  currentTime  endTime , 
   */
  useEffect(() => {
    //  
    if (isInit) {
      setIsInit(false);
    } else {
      const diff = getDiffTime(currentTime, endTime);
      setDiffTime(diff);
    }
  }, [currentTime, endTime]);

  /**
   * , 
   */
  useInterval(
    () => {
      // ,  state
      if (diffTime > 0) {
        setDiffTime(diffTime - 1);
        return;
      }

      setDiffTime(0);
    },
    active ? 1000 : null
  );

  /**
   * , , 
   */
  useEffect(() => {
    // , 0
    if (active && diffTime <= TIME_OVER) {
      // 
      setActive(false);
      // 
      console.log('useCountDown >>> , ...');
      onFinish && onFinish();
    }
  }, [diffTime]);

  return diffTime;
}

export default useCountDown;
