import React, { FC, SyntheticEvent, useCallback, useContext, useState } from 'react';
import { Button, InputItem, List } from 'antd-mobile';
import { ERROR_CODE, SMS_CODE } from '@/constants';
import classNames from 'classnames';
import SmsCodeButton from './smscode-button';
import styles from './union-pay.less';
import { ACTION_TYPE, Context, IUnionPayState, PAGE_TYPE } from './model';
import message from '@/utils/message';
import usePollingOrder from './use-polling-order';
import { unionPay } from '@/service/product-service';
import { tools } from '@/utils';
import msToast from '@/components/modal/msToast';
import monitor from '@/utils/monitor';

/**
 * 
 */
const smsCodePattern = /^([0-9]){1}(\d{5})$/;

/**
 *  
 * @param smsCodeValue 
 */
const verifySmsCode = (smsCodeValue?: string): boolean => {
  if (!smsCodeValue) {
    message.error('');
    return false;
  }
  if (!smsCodePattern.test(smsCodeValue)) {
    message.error('');
    return false;
  }
  return true;
};

/**
 * 
 *
 * ref https://open.unionpay.com/tjweb/acproduct/list?apiservId=449
 *
 */
const UnionPay: FC = () => {
  const {
    dispatch,
    pageType,
    phoneNo,
    orderNo,
    bankNo,
    smsCode: smsCodeValue,
    stageCount,
    paymentName
  }: IUnionPayState = useContext(Context);

  //   disabled
  const [disabled, updateDisabled] = useState<boolean>(true);
  //   loading
  const [loading, updateLoading] = useState<boolean>(false);

  /**
   * 
   */
  const updatePollingState = usePollingOrder(orderNo);

  /**
   * change
   */
  const handleInputChange = useCallback((value: string): void => {
    if (smsCodePattern.test(value)) {
      updateDisabled(false);
    } else {
      updateDisabled(true);
    }
    dispatch &&
      dispatch({
        type: ACTION_TYPE.UPDATE_SMS_CODE,
        payload: {
          smsCode: value
        }
      });
  }, []);

  const handlePayClick = useCallback(
    async (e: SyntheticEvent): Promise<any> => {
      e.preventDefault();
      //  
      const verifyResult = verifySmsCode(smsCodeValue);
      if (!verifyResult) {
        return;
      }
      try {
        updateDisabled(true);
        updateLoading(true);
        if (!paymentName || !bankNo || !smsCodeValue || !phoneNo || !orderNo || !stageCount) {
          monitor.log({
            message: `[API] => ${JSON.stringify({
              paymentName,
              bankNo,
              smsCodeValue,
              phoneNo
            })}`
          });
          return;
        }
        const bankStr = bankNo.replace(/\s+/g, '');
        monitor.log({
          message: `[API] => ${JSON.stringify({
            orderNo,
            paymentName,
            bankNo: bankStr,
            stageCount,
            smsCode: smsCodeValue,
            phoneNo
          })}`
        });
        // 
        const { payResultUrl } = await unionPay({
          orderNo,
          paymentName,
          bankNo: bankStr,
          stageCount,
          smsCode: smsCodeValue,
          phoneNo
        });
        monitor.log({ message: `[API] => ${payResultUrl}` });
        message.showLoading('');
        // 
        updatePollingState({ active: true, payResultUrl: payResultUrl || '' });
      } catch (error) {
        monitor.error({ message: `[API] => ${JSON.stringify(error)}` });
        const { code, msg } = error;
        switch (code) {
          case ERROR_CODE.LOGIN_REQUIRED:
            // message.error('', () => {
            //   window.location.replace(tools.getLoginUrl());
            // });
            msToast.modal({
              content: '',
              closeVisible: false,
              okText: '',
              onOk: () => {
                window.location.replace(tools.getLoginUrl());
              }
            });
            break;
          case ERROR_CODE.BIZ_ORDER_NOT_EXIST: // 
          case ERROR_CODE.BIZ_ORDER_IS_REFUND: // 
          case ERROR_CODE.BIZ_ORDER_EXPIRYTIME_OVER: // 
          case ERROR_CODE.BIZ_ORDER_IS_CLOSE: // 
          case ERROR_CODE.BIZ_ORDER_IS_PAID: // 
          case ERROR_CODE.BIZ_ORDER_IS_PAID_SUB: // 
          case ERROR_CODE.BIZ_ORDER_IS_SUB: // 
          case ERROR_CODE.BIZ_ORDER_NO_USER_ID_NOT_MATCH: // ID
          case ERROR_CODE.BIZ_ORDER_STATUS_IS_ERROR: // 
            msToast.modal({
              content: msg || '',
              closeVisible: false,
              okText: '',
              onOk: () => {
                window.location.replace(tools.getFullPath('/order'));
              }
            });
            break;
          default:
            msToast.modal({
              content: msg || '',
              closeVisible: false,
              okText: ''
            });
            break;
        }
      } finally {
        updateLoading(false);
        updateDisabled(false);
      }
    },
    [smsCodeValue]
  );

  if (pageType !== PAGE_TYPE.SMS_CODE || !orderNo) {
    return null;
  }

  return (
    <div className={styles.smsCodeContainer}>
      <List>
        <InputItem
          type="number"
          placeholder=""
          prefixListCls="bank"
          maxLength={SMS_CODE.LENGTH}
          onChange={handleInputChange}
          value={smsCodeValue}
          extra={<SmsCodeButton className={classNames(styles.btn, styles.smsBtn)} />}
        />
      </List>
      <Button className={styles.btn} type="primary" disabled={disabled} onClick={handlePayClick}>
        {(loading && '...') || ''}
      </Button>
    </div>
  );
};

export default UnionPay;
