import { createContext, Dispatch, Reducer } from 'react';
import { PAYMENT_NAME } from '@/constants';

export enum PAGE_TYPE {
  /**
   * 
   */
  BANK = 'BANK',
  /**
   * 
   */
  SMS_CODE = 'SMS_CODE',
  /**
   * 
   */
  NONE = 'none'
}

export interface IUnionPayState {
  /**
   * 
   */
  readonly pageType?: PAGE_TYPE;

  /**
   * 
   */
  readonly orderNo?: string;
  /**
   * 
   */
  readonly paymentName?: PAYMENT_NAME;
  /**
   * 
   */
  readonly stageCount?: string;
  /**
   * 
   */
  readonly bankNo?: string;
  /**
   * 
   */
  readonly phoneNo?: string;
  /**
   * 
   */
  readonly smsCode?: string;

  /**
   * 
   */
  dispatch?: Dispatch<IReducerAction>;
}

/**
 * 
 */
export const unionPayInitState: IUnionPayState = {
  pageType: PAGE_TYPE.BANK
};

/**
 * Context
 */
export const Context = createContext<IUnionPayState>(unionPayInitState);

export enum ACTION_TYPE {
  // 
  UPDATE_PHONE = 'UPDATE_PHONE',
  // 
  UPDATE_BANK_NO = 'UPDATE_BANK_NO',
  // 
  UPDATE_SMS_CODE = 'UPDATE_SMS_CODE',
  // 
  UPDATE_ORDER_INFO = 'UPDATE_ORDER_INFO',
  // 
  UPDATE_PAGE_TYPE = 'UPDATE_PAGE_TYPE'
}

export interface IReducerAction {
  type: ACTION_TYPE;
  payload: IUnionPayState;
}

/**
 * reduce
 * @param state 
 * @param action action
 */
export const reducer: Reducer<IUnionPayState, IReducerAction> = (state, action) => {
  const { type, payload } = action;
  switch (type) {
    case ACTION_TYPE.UPDATE_PHONE:
    case ACTION_TYPE.UPDATE_BANK_NO:
    case ACTION_TYPE.UPDATE_SMS_CODE:
    case ACTION_TYPE.UPDATE_ORDER_INFO:
    case ACTION_TYPE.UPDATE_PAGE_TYPE:
      return {
        ...state,
        ...payload
      };
    default:
      return state;
  }
};
