import React, { useState, FC, ReactElement } from 'react';
import classNames from 'classnames';
import dayjs from 'dayjs';

import { IListProduct } from '@/types';
import styles from './product-list.less';
import { tools } from '@/utils';
import monitor from '@/utils/monitor';
import { EVENT_CODE } from '@/constants';

export interface IListItemProps {
  // 
  product: IListProduct;
  now: string;
}

//  productId
const BASICS_PRODUCT_ID = 9;

//  productId
const ADVANCED_PRODUCT_ID = 14;

/**
 * 
 */
function renderRights(show: boolean, product: IListProduct): ReactElement {
  const className = show ? classNames(styles.rights, styles.showRights) : styles.rights;

  // 
  const elements = product.rightFormats.map((right) => {
    // 
    const desc = right.value.map((val, index) => {
      return <p key={index}>{val}</p>;
    });
    // 
    const name =
      right.name.length > 2 ? (
        <div className={styles.subject}>
          <p>{right.name.substr(0, 3)}</p>
          <p>{right.name.substr(3, right.name.length)}</p>
        </div>
      ) : (
        <div className={styles.subject}>{right.name}</div>
      );
    return (
      <div key={right.name} className={styles.item}>
        {name}
        <div className={styles.desc}>{desc}</div>
      </div>
    );
  });

  return <div className={className}>{elements}</div>;
}

/**
 * 
 * @param product 
 */
const ProductListItem: FC<IListItemProps> = ({ product, now }) => {
  // //   
  const initValue = product.name === '';

  const [show, setShow] = useState<boolean>(initValue);

  let unit = '';
  switch (product.expireUnit) {
    case 'DAY':
      unit = '';
      break;
    case 'MONTH':
      unit = '';
      break;
    case 'YEAR':
      unit = '';
      break;
  }

  // 
  let expireTime = null;
  // 0
  if (/^([1-9][0-9]*)$/.test(product.expireTime)) {
    expireTime = (
      <div className={styles.expireTime}>
        <i>(</i>
        {`${parseInt(product.expireTime, 10)}${unit}`}
        <i>)</i>
      </div>
    );
  }

  // *
  let classHour: ReactElement | string = (
    <>
      <span>(</span>
      <i>{product.count}</i>
      <span>)</span>
    </>
  );

  //  / 
  const nowTime = dayjs(now).valueOf();
  const fixedTime = dayjs('2019-10-01 00:00:00').valueOf();

  /**
   * 10
   * 
   */
  if (nowTime > fixedTime) {
    classHour = product.productId === BASICS_PRODUCT_ID ? '' : <span>()</span>;
  }

  /**
   * 
   */
  function handleToggle(): void {
    setShow(!show);
  }

  function handleClick() {
    monitor.log({
      eventCode: EVENT_CODE.CLICK_PRODUCT_DETAIL,
      message: ` '', `
    });
    window.location.href = tools.getFullPath(`/product/${product.productId}`);
  }

  return (
    <div
      className={classNames(styles.product, {
        [styles.advanced]: product.productId === ADVANCED_PRODUCT_ID
      })}
    >
      <div className={styles.switch}>
        <div className={styles.title}>
          {product.name}
          {classHour}
          {expireTime}
        </div>
        <div className={styles.toggle}>
          <span onClick={handleToggle}>{show ? '' : ''}</span>
        </div>
      </div>
      {renderRights(show, product)}
      {product.deductibleAmountStr && (
        <div className={styles.elevenTip}>{product.deductibleAmountStr}</div>
      )}
      <div className={styles.buy} onClick={handleClick}>
        
      </div>
    </div>
  );
};

export default ProductListItem;
