import React, { FC } from 'react';

import { formatDiffTime } from '@/utils/tools';
import { useCountDown } from '@/components/hooks';
import styles from './product.less';

interface ICountDownProps {
  currentTime: string;
  endTime: string;
  onFinish?: () => void;
}

/**
 * 
 */
const CountDown: FC<ICountDownProps> = ({ currentTime, endTime, onFinish }) => {
  const countDownTime = useCountDown(currentTime, endTime, onFinish);
  return <div className={styles.payTime}>{formatDiffTime(countDownTime)}</div>;
};

export default CountDown;
