import React, { FC, useState } from 'react';
import classNames from 'classnames';

import { IPaymentName, IPaymentStage } from '@/types/payment';
import styles from './payment.less';

interface IPaymentMethodItemProps {
  selected: boolean; // 
  stageCount: string; // 
  item: IPaymentName; // 
  onSelected: (selectedName: string, stageCount: string) => void; // 
}

/**
 * , : |||...
 */
const PaymentMethodItem: FC<IPaymentMethodItemProps> = (props) => {
  const { item, selected, stageCount, onSelected } = props;

  /**
   * UI
   * 'no' | 'open' | 'close'
   *  |  | 
   */
  const [status, setStatus] = useState(() => {
    // , 
    if (item.stageType === 'no') {
      return 'no';
    }
    // , , 
    if (selected) {
      return 'open';
    }
    // ,   'close'
    return item.stageType;
  });

  /**
   *  
   *  
   */
  const handleClickName = () => {
    if (status === 'no') {
      // , ,  : '', 
      onSelected(item.paymentName, '');
      return;
    }

    /**
     * 
     */
    if (selected) {
      if (status === 'open') {
        setStatus('close');
      } else {
        setStatus('open');
      }
    } else {
      /**
       * , 
       */
      if (status === 'close') {
        setStatus('open');
      }
    }

    /**
     * , 
     */
    if (!selected) {
      onSelected(item.paymentName, item.orderStages[0].stageCount);
    }
  };

  /**
   *  
   */
  const handleClickStage = (stage: IPaymentStage) => {
    onSelected(item.paymentName, stage.stageCount);
  };

  // 
  let selectClass = '';
  if (selected) {
    selectClass = classNames(styles.select, styles.check);
  } else {
    selectClass = classNames(styles.select, styles.noCheck);
  }

  let stageElement = null;
  // , 
  if (status === 'open') {
    stageElement = item.orderStages.map((stage, index) => {
      // 
      let stageClass = classNames(styles.stage);
      if (selected && stage.stageCount === stageCount) {
        stageClass = classNames(styles.stage, styles.selectStage);
      }

      return (
        <div key={index} className={stageClass} onClick={handleClickStage.bind(null, stage)}>
          <span>{stage.stageCount}</span>
          <span>
            <i></i>
            {stage.eachAmount}
          </span>
          <div className={styles.total}>
            :
            <span>
              <i></i>
              {stage.totalAmount}
            </span>
          </div>
          <div className={styles.rate}>
            :<span>{stage.interestRate}</span>
          </div>
        </div>
      );
    });
  }

  // 00
  let noInterestElement = null;
  if (item.noInterest === true) {
    noInterestElement = <span className={styles.noInterest}>00</span>;
  }

  return (
    <div key={item.paymentName} className={styles[item.paymentName]}>
      <div onClick={handleClickName}>
        {item.paymentText}
        {noInterestElement}
        <span className={selectClass} />
      </div>
      {stageElement}
    </div>
  );
};

// 
export default React.memo(PaymentMethodItem, (nextProps, prevProps) => {
  // return true, 
  // return false, 

  //      
  if (nextProps.selected !== prevProps.selected) {
    return false;
  }

  //   , : , 36
  if (nextProps.selected && nextProps.stageCount !== prevProps.stageCount) {
    return false;
  }

  return true;
});
