import { useEffect } from 'react';

import { IPaymentInfo, ISelectMethod } from '@/types/payment';
import { PAYMENT_NAME } from '@/constants';
import message from '@/utils/message';

/**
 * H5, : H5, H5
 * :  
 * @param selectMethod 
 */
const useAliPay = (paymentInfo: IPaymentInfo, selectMethod: ISelectMethod | null) => {
  useEffect(() => {
    if (selectMethod === null) {
      return;
    }

    // H5, : H5, H5
    if (
      selectMethod.paymentName === PAYMENT_NAME.AlipayWap ||
      selectMethod.paymentName === PAYMENT_NAME.AlipayHB
    ) {
      // h5 formhtml
      if (!paymentInfo || !paymentInfo.form) {
        message.error('');
        return;
      }
      const div = document.createElement('div');
      div.innerHTML = decodeURIComponent(paymentInfo.form);
      document.body.appendChild(div);

      // form, 
      const forms = document.getElementsByTagName('form');
      const alipayForm = Array.prototype.find.call(
        forms,
        (item: { name: string }) => item.name === 'punchout_form'
      );
      alipayForm && alipayForm.submit && alipayForm.submit();
    }
  }, [selectMethod]);
};

export default useAliPay;
