import dayjs from 'dayjs';

import { appConfig } from '../config';
import { requestUtils } from '../utils';
import { IBaseOrder, IOrderDetail, IAgreement, IOrder, ICourse } from '../types';
import { PRODUCT_TYPE } from '@/constants';

const apiDomain = appConfig.domain.apiDomain;

/**
 *  2019101,  ,  3999, 
 * @param orderInfo   
 */
const processOrderDetail = (orderInfo: IOrderDetail) => {
  // 2019101
  const flagTime = dayjs('2019-10-01 00:00:00');
  // 
  const createTime = dayjs(orderInfo.createTime);

  /**
   *  2019101,  
   * 3999, 
   */
  if (!isNaN(createTime.valueOf()) && createTime > flagTime) {
    /**
     * 
     * 3999, 
     */
    let classGroup: ICourse | null = {
      code: 'CLASS_GROUP',
      name: '',
      count: 2,
      unit: ''
    };

    switch (orderInfo.productType) {
      case PRODUCT_TYPE.INTERNAL_COURSE: // 3999  6
        classGroup.count = '6';
        break;
      case PRODUCT_TYPE.INTERNAL_RISE_BASE_COURSE: // 3999  2
        classGroup.count = '2';
        break;
      case PRODUCT_TYPE.TRAIN_RISE_BASE_COURSE: //  8
      case PRODUCT_TYPE.YEAR_COURSE: //   8
      case PRODUCT_TYPE.UPGRADE_BIG_COURSE: //   8
        classGroup.count = '8';
        break;
      case PRODUCT_TYPE.TWO_YEARS_COURSE: //   16
      case PRODUCT_TYPE.UPGRADE_THREE_YEAR_COURSE: //   16
        classGroup.count = '16';
        break;
      case PRODUCT_TYPE.UPGRADE_TRAIN_TO_HEGEMONY_COURSE: //   32
      case PRODUCT_TYPE.THREE_YEARS_COURSE: //   32
        classGroup.count = '32';
        break;
      default:
        classGroup = null;
        break;
    }

    if (classGroup) {
      // 
      if (!Array.isArray(orderInfo.giveCourseList)) {
        orderInfo.giveCourseList = [];
      }
      orderInfo.giveCourseList.push(classGroup);
    }
  }
};

/**
 *  2019101,  ,  3999, 
 * @param orderInfo   
 */
const processOrder = (orderInfo: IOrder) => {
  // 2019101
  const flagTime = dayjs('2019-10-01 00:00:00');
  // 
  const createTime = dayjs(orderInfo.createTime);

  /**
   *  2019101,  
   * 3999, 
   */
  if (!isNaN(createTime.valueOf()) && createTime > flagTime) {
    /**
     * 
     * 3999, 
     */
    let classGroup: ICourse | null = {
      code: 'CLASS_GROUP',
      name: '',
      count: 2,
      unit: ''
    };

    switch (orderInfo.productType) {
      case PRODUCT_TYPE.INTERNAL_COURSE: // 3999  6
        classGroup.count = '6';
        break;
      case PRODUCT_TYPE.INTERNAL_RISE_BASE_COURSE: // 3999  2
        classGroup.count = '2';
        break;
      case PRODUCT_TYPE.TRAIN_RISE_BASE_COURSE: //  8
      case PRODUCT_TYPE.YEAR_COURSE: //   8
      case PRODUCT_TYPE.UPGRADE_BIG_COURSE: //   8
        classGroup.count = '8';
        break;
      case PRODUCT_TYPE.TWO_YEARS_COURSE: //   16
      case PRODUCT_TYPE.UPGRADE_THREE_YEAR_COURSE: //   16
        classGroup.count = '16';
        break;
      case PRODUCT_TYPE.UPGRADE_TRAIN_TO_HEGEMONY_COURSE: //   32
      case PRODUCT_TYPE.THREE_YEARS_COURSE: //   32
        classGroup.count = '32';
        break;
      default:
        classGroup = null;
        break;
    }

    if (classGroup && orderInfo.activityAndCourse) {
      // 
      if (!Array.isArray(orderInfo.activityAndCourse.giveCourseList)) {
        orderInfo.activityAndCourse.giveCourseList = [];
      }
      orderInfo.activityAndCourse.giveCourseList.push(classGroup);
    }
  }
};

/**
 * 
 * @param wxToken token
 * @param passToken passtoken
 */
export const getOrderList = (): Promise<IBaseOrder[]> => {
  const url = apiDomain.concat('/usercenter/order/list');
  return requestUtils.get(url).then((res) => {
    return res.data.map((item: IBaseOrder) => {
      let orderList = item.orderList;
      if (Array.isArray(orderList)) {
        orderList = orderList.map((sonOrder: IOrder) => {
          //  2019101,  ,  3999, 
          processOrder(sonOrder);

          return {
            ...sonOrder,
            name: sonOrder.name.replace(/|/g, '')
          };
        });
      }
      return {
        ...item,
        orderName: item.orderName.replace(/|/g, ''),
        orderList
      };
    });
  });
};

/**
 * 
 * @param orderNo 
 * @param wxToken token
 * @param passToken passtoken
 */
export const getOrderById = (orderNo: string): Promise<IOrderDetail> => {
  const url = apiDomain.concat('/usercenter/order/detail');
  const params = {
    orderNo
  };
  return requestUtils.get(url, params).then((res) => {
    const orderInfo = res.data;
    if (orderInfo && orderInfo.name) {
      orderInfo.name = orderInfo.name.replace(/|/g, '');
    }

    //  2019101,  ,  3999, 
    processOrderDetail(orderInfo);

    return orderInfo;
  });
};

