import { useEffect } from 'react';

/**
 * 
 * @param outCallback 
 * @param inCallback 
 */
function useVisibilityChange(outCallback?: () => void, inCallback?: () => void) {
  useEffect(() => {
    const onChangeEvent = () => {
      if (document.hidden) {
        // 
        outCallback && outCallback();
      } else {
        // 
        inCallback && inCallback();
      }
    };

    document.addEventListener('visibilitychange', onChangeEvent);
    return () => {
      document.removeEventListener('visibilitychange', onChangeEvent);
    };
  }, []);
}

export default useVisibilityChange;
