import { createContext, Reducer } from 'react';

import { IOrderDetail, IBaseOrder } from '@/types';

/**
 * action 
 */
export enum ACTION_TYPE {
  /**
   * 
   */
  UPDATE_ORDER_INFO = 'UPDATE_ORDER_INFO',
  /**
   * 
   */
  UPDATE_ORDER_LIST = 'UPDATE_ORDER_LIST',
  /**
   * 
   */
  UPDATE_ORDER_STATUS = 'UPDATE_ORDER_STATUS'
}

export interface IOrderState {
  orderDetail?: IOrderDetail;
  orderList?: IBaseOrder[];
}
export interface IReducerAction {
  type: ACTION_TYPE;
  payload: IOrderState;
}

/**
 * 
 */
export const InitOrderState: IOrderState = {};

const initialContext = () => {};
/**
 * Context
 */
export const OrderContext = createContext<() => void>(initialContext);

/**
 * reducer
 * @param state 
 * @param action Action
 */
export const reducer: Reducer<IOrderState, IReducerAction> = (state, action) => {
  const {
    type,
    payload: { orderDetail, orderList }
  } = action;
  switch (type) {
    case ACTION_TYPE.UPDATE_ORDER_INFO:
    case ACTION_TYPE.UPDATE_ORDER_STATUS:
      return {
        ...state,
        orderDetail
      };
    case ACTION_TYPE.UPDATE_ORDER_LIST:
      return {
        ...state,
        orderList
      };
    default:
      return state;
  }
};

