import { Toast } from 'antd-mobile';

import monitor from './monitor';

/**
 * 0, 
 */
const loadingDuration = 0;

/**
 * 
 */
const duration = 1.5;

const message = {
  /**
   *  
   */
  info: (content: string, callback?: () => void) => {
    Toast.info(content, duration, callback);
  },
  /**
   *  
   */
  success: (content: string, callback?: () => void) => {
    Toast.success(content, duration, callback);
  },
  /**
   *  
   */
  error: (content: string, callback?: () => void) => {
    Toast.info(content, duration, callback);
  },
  /**
   *  
   */
  offline: (content: string, callback?: () => void) => {
    Toast.offline(content, duration, callback);
  },
  /**
   *  Loading, 
   */
  showLoading: (content = '...') => {
    try {
      Toast.loading(content, loadingDuration);
    } catch (error) {
      monitor.error({
        message: `showLoading >>> error, ${JSON.stringify(error)}`
      });
    }
  },
  /**
   *  Loading
   */
  closeLoading: () => {
    try {
      Toast.hide();
    } catch (error) {
      monitor.error({
        message: `closeLoading >>> error, ${JSON.stringify(error)}`
      });
    }
  }
};

export default message;
