import React, { FC, useEffect, useReducer } from 'react';
import styles from './union-pay.less';
import { ACTION_TYPE, Context, IUnionPayState, reducer, unionPayInitState } from './model';
import queryString from 'query-string';

import SmsCode from './sms-code';
import BankCard from './bank-card';
import { EVENT_CODE, PAYMENT_NAME, PAYMENT_TEXT } from '@/constants';
import useCheckWechatAuth from '@/components/hooks/useCheckWechatAuth';
import msToast from '@/components/modal/msToast';
import useViewEvent from '@/components/hooks/useViewEvent';
import monitor from '@/utils/monitor';
import useWechatSdk from '@/components/hooks/useWechatSdk';
import { shareConfig } from '@/config';
import { getFullPath } from '@/utils/tools';
import classNames from 'classnames';

const defaultShareOptions = shareConfig.default;

interface IUrlParams {
  /**
   * 
   */
  readonly orderNo?: string;
  /**
   * 
   */
  readonly method?: PAYMENT_NAME;
  /**
   * 
   */
  readonly stageCount?: string;
  /**
   * 
   * @description formbankNo
   */
  readonly bankNo?: string;
}

/**
 * 
 * ref https://open.unionpay.com/tjweb/acproduct/list?apiservId=449
 */
const UnionPay: FC = () => {
  const [state, dispatch] = useReducer(reducer, unionPayInitState);

  /**
   * 
   */
  useCheckWechatAuth({ support: 'all' });

  /**
   * JS-SDK, 
   */
  useWechatSdk({ shareStatus: true, shareOptions: defaultShareOptions });

  useEffect(() => {
    //  url  , , , versionId
    const orderInfo: IUrlParams = queryString.parse(window.location.search);
    if (!orderInfo || !orderInfo.orderNo || !orderInfo.method || !orderInfo.stageCount) {
      monitor.log({ message: ` => ${JSON.stringify(orderInfo)}` });
      msToast.modal({
        content: '',
        okText: '',
        closeVisible: false,
        onOk: () => {
          window.location.replace(getFullPath('/order'));
        }
      });
      return;
    }
    let bankNoOfUrlParams = '';
    if (orderInfo.bankNo) {
      bankNoOfUrlParams = orderInfo.bankNo.replace(/\D/g, '').replace(/(....)(?=.)/g, '$1 ');
    }
    // 
    dispatch({
      type: ACTION_TYPE.UPDATE_ORDER_INFO,
      payload: {
        ...orderInfo,
        paymentName: orderInfo.method,
        bankNo: bankNoOfUrlParams
      }
    });
  }, []);

  /**
   * , 
   */
  useViewEvent({
    eventCode: EVENT_CODE.VIEW_UNION_PAY,
    message: ''
  });

  useEffect(() => {
    monitor.log({ message: ` => ${state.pageType}` });
  }, [state.pageType]);

  const contextValue: Partial<IUnionPayState> = { ...state, dispatch };

  let paymentText = null;
  let paymentName = '';
  if (state.paymentName) {
    paymentName = (state.paymentName as string).toUpperCase();
    paymentText = PAYMENT_TEXT[paymentName as keyof typeof PAYMENT_TEXT];
  }

  return (
    <Context.Provider value={contextValue}>
      <div className={styles.bankContainer}>
        {/*  */}
        {state.paymentName && (
          <div className={classNames(styles.bankName, styles.bankIcon, styles[paymentName])}>
            {paymentText}
          </div>
        )}
        {/*  */}
        <BankCard />
        {/*  */}
        <SmsCode />
      </div>
    </Context.Provider>
  );
};

export default UnionPay;
