import { useEffect } from 'react';

import { IWechatPayOptions } from '@/types/wechat';
import { chooseWXPay } from '@/utils/wechat-utils';
import message from '@/utils/message';
import monitor from '@/utils/monitor';

/**
 * 
 * : 
 */
const useWechatPay = (
  wechatPayOptions: IWechatPayOptions | undefined,
  onReloadData?: () => void
) => {
  useEffect(() => {
    if (!wechatPayOptions) {
      return;
    }

    // JS-SDK 
    chooseWXPay({
      appId: wechatPayOptions.appId,
      timestamp: wechatPayOptions.timestamp,
      nonceStr: wechatPayOptions.nonceStr,
      package: wechatPayOptions.package,
      signType: wechatPayOptions.signType,
      paySign: wechatPayOptions.paySign,
      success: () => {
        monitor.log({
          message: `useWechatPay >>> , ...`
        });
        if (wechatPayOptions.payResultUrl) {
          window.location.replace(wechatPayOptions.payResultUrl);
        }
      },
      cancel: () => {
        monitor.log({
          message: `useWechatPay >>> `
        });
        // , , 
        onReloadData && onReloadData();
      },
      fail: ({ msg }) => {
        monitor.error({
          message: `useWechatPay >>> , ${msg}`
        });
        message.error(msg);
        // , , 
        onReloadData && onReloadData();
      }
    });
  }, [wechatPayOptions]);
};

export default useWechatPay;
