import wx from 'weixin-js-sdk';

import monitor from '@/utils/monitor';
import message from '@/utils/message';
import { IShareOptions, IShareStatus, IWechatPayOptions, IWechatSign } from '@/types/wechat';

// const debugStatus = appConfig.env === 'production' ? false : true;
const debugStatus = false;

/**
 * 
 */
export const wechatStatus = {
  /**
   * config
   */
  ready: false,
  /**
   * config
   */
  error: ''
};

/**
 * , config
 * JS-SDK
 * @param wechatSign JS-SDK 
 * @param shareStatus 
 * @param shareOptions 
 */
export const wechatConfig = (
  wechatSign: IWechatSign,
  shareStatus: IShareStatus,
  shareOptions: IShareOptions = {}
) => {
  const config = {
    debug: debugStatus, // ,apialertpclogpc
    appId: wechatSign.appId, // 
    timestamp: wechatSign.timestamp, // 
    nonceStr: wechatSign.nonceStr, // 
    signature: wechatSign.signature, // 
    jsApiList: [
      'chooseWXPay',
      'chooseImage',
      'onMenuShareTimeline',
      'onMenuShareAppMessage',
      'onMenuShareQQ',
      'onMenuShareWeibo',
      'onMenuShareQZone',
      'hideMenuItems',
      'showMenuItems',
      'hideOptionMenu'
    ] // JS
  };

  wx.config(config);
  wechatReady(shareStatus, shareOptions);
  wechatError();
};

/**
 * , ready
 * @param shareStatus 
 * @param shareOptions 
 */
const wechatReady = (shareStatus: IShareStatus, shareOptions: IShareOptions) => {
  wx.ready(() => {
    console.log(' js-sdk ');
    // configreadyconfig
    // configready
    // ready
    wechatStatus.ready = true;
    wechatStatus.error = '';

    // 
    configShare(shareStatus, shareOptions);
  });
};

/**
 * 
 */
export const configShare = (shareStatus: IShareStatus, shareOptions: IShareOptions) => {
  let defaultLink = window.location.href;
  let options: IShareOptions = {
    title: '', // 
    desc: '', // 
    link: defaultLink, // 
    type: 'link', // ,musicvideolinklink
    dataUrl: '', // typemusicvideo
    ...shareOptions
  };

  console.log('', ': ' + shareStatus, options);

  onMenuShareTimeline(options);
  onMenuShareAppMessage(options);
  onMenuShareQQ(options);
  onMenuShareWeibo(options);
  onMenuShareQZone(options);

  //   
  if (shareStatus === true || shareStatus === false) {
    showOrHideMenuShare(shareStatus);
  }
};

/**
 * , error
 */
const wechatError = () => {
  wx.error((res: any) => {
    console.error(' js-sdk ');
    // configerrorconfigdebugresSPA
    wechatStatus.ready = false;
    wechatStatus.error = JSON.stringify(res);
  });
};

/**
 * 
 */
export const chooseWXPay = (options: IWechatPayOptions) => {
  // JS-SDK
  if (!wechatStatus.ready) {
    message.error('JS-SDK, ');
    return;
  }

  wx.chooseWXPay({
    appId: options.appId,
    timestamp: options.timestamp,
    nonceStr: options.nonceStr,
    package: options.package,
    signType: options.signType,
    paySign: options.paySign,
    success(res: { errMsg: string }) {
      console.log('chooseWXPay >>> success', res);
      if (res.errMsg === 'chooseWXPay:ok') {
        options.success && options.success();
      } else {
        options.fail && options.fail({ msg: res.errMsg });
      }
    },
    cancel() {
      options.cancel && options.cancel();
    },
    fail(res: any) {
      console.log('chooseWXPay >>> fail', res);
      // 
      monitor.log({
        message: 'wx.chooseWXPay.fail. ' + JSON.stringify(res)
      });
      options.fail && options.fail({ msg: (res && res.errMsg) || '' });
    }
  });
};

/**
 * 
 * @param count 
 * @param callback 
 */
export const chooseImage = ({ count = 1, callback }: any) => {
  if (!wechatStatus.ready) {
    console.error('>>>');
    return;
  }
  wx.chooseImage({
    count, // 9
    sizeType: ['compressed'], // 
    sourceType: ['album', 'camera'], // 
    success: (res: any) => {
      callback && callback(res.localIds);
    }
  });
};

/**
 *   
 */
export const showOrHideMenuShare = (showShare: boolean) => {
  if (showShare) {
    wx.showMenuItems({
      menuList: [
        'menuItem:share:appMessage',
        'menuItem:share:timeline',
        'menuItem:share:qq',
        'menuItem:share:weiboApp',
        'menuItem:share:facebook',
        'menuItem:share:QZone',
        'menuItem:favorite',
        'menuItem:openWithQQBrowser',
        'menuItem:openWithSafari'
      ]
    });
  } else {
    wx.hideMenuItems({
      menuList: [
        'menuItem:share:appMessage',
        'menuItem:share:timeline',
        'menuItem:share:qq',
        'menuItem:share:weiboApp',
        'menuItem:share:facebook',
        'menuItem:share:QZone',
        'menuItem:favorite',
        'menuItem:openWithQQBrowser',
        'menuItem:openWithSafari'
      ] // menu3
    });
  }
};

