import React, { FC } from 'react';

import { tools } from '@/utils';
import defaultPhoto from '../../public/images/common/photo.png';
import styles from './user-info.less';

export interface IUserInfoProps {
  studentId?: number;
  studentName?: string;
  phone?: string;
  photo?: string;
}

/**
 * , 
 */
const handleChangeAccount = () => {
  window.location.replace(tools.getLoginUrl());
};

/**
 * 
 */
export const UserInfo: FC<IUserInfoProps> = ({ studentId, studentName, phone }) => {
  return (
    <div className={styles.box}>
      <div className={styles.photo}>
        <img src={defaultPhoto} />
      </div>
      <div className={styles.change} onClick={handleChangeAccount}>
        
      </div>
      <div className={styles.row}>
        <div className={styles.nickName}>{studentName}</div>
        <div className={styles.id}>ID:{studentId}</div>
      </div>
      <div className={styles.row}>
        <div className={styles.phone}>: {phone}</div>
      </div>
    </div>
  );
};
