import { useEffect } from 'react';

import { IShareOptions, IShareStatus } from '../../types/wechat';
import { ACCOUNT_TYPE } from '../../constants';
import * as tools from '../../utils/tools';
import * as wechatUtils from '../../utils/wechat-utils';
import * as wechatService from '../../service/wechat-service';
import monitor from '@/utils/monitor';

const env = process.env.NODE_ENV;

export interface IUseWechatSdkProps {
  /**
   * , 
   */
  account?: ACCOUNT_TYPE;
  /**
   * 
   */
  shareStatus?: IShareStatus;
  /**
   * 
   */
  shareOptions?: IShareOptions;
}

/**
 * JS-SDK
 * @param options 
 */
const useWechatSdk = (options: IUseWechatSdkProps = {}) => {
  const { account = ACCOUNT_TYPE.MAIN, shareStatus = 'ignore', shareOptions = {} } = options;
  const { title, link } = shareOptions;

  /**
   * , JS-SDK
   * : , , , , 
   */
  useEffect(() => {
    const init = async () => {
      //  
      if (env === 'development' || env === 'dev') {
        console.log('useWechatSdk >>>  ');
        return;
      }

      // , JS-SDK
      if (!tools.isWeChat()) {
        console.log('useWechatSdk >>> , JS-SDK');
        return;
      }

      const ready = wechatUtils.wechatStatus.ready;

      /**
       * JS-SDK
       * || tools.isAndroid()     
       */
      if (!ready) {
        try {
          const wechatSign = await wechatService.getWechatSignature(account);
          wechatUtils.wechatConfig(wechatSign, shareStatus, shareOptions);
        } catch (error) {
          monitor.error({
            message: `useWechat >> , ${JSON.stringify(error)}`
          });
        }
      } else {
        wechatUtils.configShare(shareStatus, shareOptions);
      }
    };

    init();
  }, [shareStatus, title, link]);
};

export default useWechatSdk;
