import React, { FC, useState, useEffect } from 'react';
import ReactDOM from 'react-dom';

import * as tools from '@/utils/tools';
import styles from './modal.less';
import rabbit from '@/public/images/common/modal-rabbit.png';
import useStopBodyScroll from '@/components/hooks/useStopBodyScroll';
interface ModalProps {
  className?: string;
  title?: React.ReactNode | string;
  footer?: React.ReactNode | string;
  closeText?: string;
  okText?: string;
  visible: boolean;
  closeVisible?: boolean;
  okVisible?: boolean;
  onClose?: () => void | boolean | Promise<any>;
  onOk?: () => void | boolean | Promise<any>;
  onToastClose?: () => void;
}

/**
 * Modal
 */
export const NoPortalModal: FC<ModalProps> = ({
  className,
  title,
  footer,
  closeText,
  okText,
  visible,
  closeVisible,
  okVisible,
  onClose,
  onOk,
  onToastClose,
  children
}) => {
  const [modalVisible, setVisible] = useState(visible);
  const [closeDisabled, setCloseDisabled] = useState(false);
  const [okDisabled, setOkDisabled] = useState(false);
  useEffect(() => {
    setVisible(visible);
  }, [visible]);

  /**
   * 
   */
  useStopBodyScroll(true);
  /**
   * modal
   */
  const handleClose = () => {
    const result = onClose && onClose();
    if (tools.isPromise(result)) {
      setCloseDisabled(true);
      (result as Promise<any>)
        .then((res) => {
          onToastClose && onToastClose();
        })
        .catch((error) => {
          setCloseDisabled(false);
        });
      return;
    }
    if (result === false) {
      return;
    }
    onToastClose && onToastClose();
  };

  /**
   * 
   */
  const handleOk = () => {
    const result = onOk && onOk();
    if (tools.isPromise(result)) {
      setOkDisabled(true);
      (result as Promise<any>)
        .then((res) => {
          onToastClose && onToastClose();
        })
        .catch((error) => {
          setOkDisabled(false);
        });
      return;
    }
    if (result === false) {
      return;
    }
    onToastClose && onToastClose();
  };

  if (!modalVisible) {
    return null;
  }
  return (
    <>
      <div className={styles['mask']} />
      <div className={className ? styles[className] : styles['modal-wrap']}>
        <div className={styles['modal']}>
          <div className={styles['modal-header']}>
            <img src={rabbit} alt="logo" />
          </div>
          <Title title={title} />
          <div className={styles['modal-content']}>{children}</div>
          <Footer
            footer={footer}
            closeText={closeText}
            okText={okText}
            closeVisible={closeVisible}
            okVisible={okVisible}
            closeDisabled={closeDisabled}
            okDisabled={okDisabled}
            onClose={handleClose}
            onOk={handleOk}
          />
        </div>
      </div>
    </>
  );
};

/**
 * ProtalBodyModal
 */
export const Modal: FC<ModalProps> = ({ ...props }) => {
  return (
    <Portal>
      <NoPortalModal {...props} />
    </Portal>
  );
};

interface TitleProps {
  title?: React.ReactNode | string;
}

/**
 * modaltitle
 */
const Title: FC<TitleProps> = ({ title }): any => {
  if (typeof title === 'string') {
    return <div className={styles['modal-title']}>{title}</div>;
  }
  return title || null;
};

interface FooterProps {
  footer?: React.ReactNode;
  closeText?: string;
  okText?: string;
  closeVisible?: boolean;
  okVisible?: boolean;
  closeDisabled?: boolean;
  okDisabled?: boolean;
  onClose?: () => void;
  onOk?: () => void;
}

/**
 * modalFooter
 */
const Footer: FC<FooterProps> = ({
  footer,
  closeText = '',
  okText = '',
  closeVisible = true,
  okVisible = true,
  onClose,
  onOk,
  closeDisabled = false,
  okDisabled = false
}): any => {
  if (footer) {
    return footer;
  }

  const handleClose = () => {
    if (closeDisabled) {
      return false;
    }
    onClose && onClose();
  };

  const handleOk = () => {
    if (okDisabled) {
      return false;
    }
    onOk && onOk();
  };

  const buttons = [];
  if (closeVisible) {
    buttons.push(
      <button key={'close'} onClick={handleClose} className={styles['modal-operator-close']}>
        {closeText}
      </button>
    );
  }
  if (okVisible) {
    buttons.push(
      <button key={'ok'} onClick={handleOk} className={styles['modal-operator-confirm']}>
        {okText}
      </button>
    );
  }
  return <div className={styles['modal-operator']}>{buttons}</div>;
};

/**
 * Portal
 */
const Portal: FC = ({ children }) => {
  const [container] = useState(() => {
    return document.createElement('div');
  });

  useEffect(() => {
    document.body.appendChild(container);
    return () => {
      document.body.removeChild(container);
    };
  }, []);
  return ReactDOM.createPortal(children, container);
};
