import React, { FC, useEffect, useState } from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import qs from 'query-string';

import { Loading } from '@/components/loading';
import EmptyPage from '@/views/components/empty-page';

import * as orderService from '@/service/order-service';
import monitor from '@/utils/monitor';
import { EVENT_CODE } from '@/constants/events';

interface IQuerySearch {
  id?: string;
  //    =>     preview => 
  source?: 'preview';
}

const initialData = {
  htmlContent: '',
  loading: true,
  isError: false,
  errorMessage: ''
};
/**
 * 
 */
const Agreement: FC<RouteComponentProps> = (props) => {
  const [state, setState] = useState(() => {
    return initialData;
  });
  const { loading, isError, errorMessage } = state;
  const { id, source }: IQuerySearch = qs.parse(location.search) || {};

  /**
   * 
   */
  useEffect(() => {
    if (source === 'preview') {
      monitor.log({
        eventCode: EVENT_CODE.VIEW_AGREEMENT_DETAIL__PREVIEW,
        message: '()'
      });
    } else {
      monitor.log({
        eventCode: EVENT_CODE.VIEW_AGREEMENT_DETAIL__ORDER,
        message: '()'
      });
    }
  }, []);

  useEffect(() => {
    // if (!id) {
    //   setState((state) => ({ ...state, isError: true, errorMessage: ': id' }));
    //   return;
    // }
    // iframeDOM
    const $iframe: HTMLIFrameElement = document.createElement('iframe');
    const $root = document.getElementById('root') as HTMLDivElement;
    // iframe 
    $iframe.style.minHeight = '100%';
    $iframe.style.width = '100%';
    $iframe.style.border = 'none';
    // dom
    if ($root) {
      $root.appendChild($iframe);
    }
    const agreementPromise = id ? orderService.getAgreement(id) : orderService.getNewAgreement();

    agreementPromise
      .then((data) => {
        let content = '';
        if (data) {
          content = data.htmlContent || '';
        }
        if (content && $iframe.contentDocument) {
          $iframe.contentDocument.write(buildPreviewHtml(content));
        }
        setState({
          ...state,
          htmlContent: content,
          loading: false
        });
      })
      .catch((error) => {
        $root.removeChild($iframe);
        const { msg } = error || {
          msg: ''
        };
        setState({
          ...state,
          isError: true,
          errorMessage: msg,
          loading: false
        });
      });
    return () => {
      if ($root) {
        $root.removeChild($iframe);
      }
    };
  }, []);

  if (loading) {
    return <Loading />;
  }
  if (isError) {
    return <EmptyPage describe={errorMessage} />;
  }
  return null;
  // return <div dangerouslySetInnerHTML={{ __html: buildPreviewHtml(htmlContent) }} />;
};

export default withRouter(Agreement);

const buildPreviewHtml = (html: string) => {
  return `
    <!Doctype html>
    <html>
      <head>
      <title></title>
      <style>
        html,body{
          height: 100%;
          margin: 0;
          padding: 0;
          overflow: auto;
          background-color: #f1f2f3;
          -webkit-overflow-scrolling: touch;
        }
        .container{
          box-sizing: border-box;          
          max-width: 100%;
          min-height: 100%;
          margin: 0 auto;
          padding: 30px 20px;
          background-color: #fff;
          border-right: solid 1px #eee;
          border-left: solid 1px #eee;
        }
        .container img,
        .container audio,
        .container video{
          max-width: 100%;
          height: auto;
        }
        .container p{
          white-space: pre-wrap;
          min-height: 1em;
        }
        .container pre{
          padding: 15px;
          background-color: #f1f1f1;
          border-radius: 5px;
        }
        .container blockquote{
          margin: 0;
          padding: 15px;
          background-color: #f1f1f1;
          border-left: 3px solid #d1d1d1;
        }
      </style>
      <body>
          <div class="container">${html}</div>
      </body>
    </html>
  `;
};
