import { useCallback, useEffect, useState } from 'react';

/**
 * 
 */
type ISetStatus = (status: boolean) => void;

/**
 * , 
 * @param count , 
 * @param onFinish 
 * @param reset  
 */
function useSimpleCountDown(
  count: number,
  onFinish?: () => void,
  reset = true
): [number, ISetStatus] {
  // 
  const [diffTime, setDiffTime] = useState(count);
  /**
   * , 
   */
  const [active, setActive] = useState<boolean>(false);

  /**
   * 
   */
  const setStatus = useCallback((status: boolean) => {
    // 
    if (status || reset) {
      setDiffTime(count);
    }
    // 
    setActive(status);
  }, []);

  /**
   * 
   */
  useEffect(() => {
    if (!active) {
      return;
    }

    const timer = setInterval(() => {
      setDiffTime((prevDiff) => {
        if (prevDiff > 0) {
          return prevDiff - 1;
        }

        setStatus(false);
        onFinish && onFinish();
        return 0;
      });
    }, 1000);

    return () => {
      clearInterval(timer);
    };
  }, [active]);

  return [diffTime, setStatus];
}

export default useSimpleCountDown;
