import React, { FC } from 'react';

import { IDetailProduct } from '@/types';
import styles from './product.less';
import CoursePackIcon from '@/views/components/course-pack-icon';
import { PRODUCT_TYPE } from '@/constants';
import { tools } from '@/utils';
import { appConfig } from '@/config';

/**
 * 
 */
const { elevenActivityUrl } = appConfig.resources;

/**
 * , 
 */
const UNPAID_STATUS = 'UNPAID';

interface IProductDetailProps {
  product: IDetailProduct;
}

/**
 * 
 */
const ProductDetail: FC<IProductDetailProps> = ({ product }) => {
  /**
   * 
   */
  let couponElement = null;
  if (product.coupons.length) {
    couponElement = product.coupons.map((item, index) => {
      return (
        <div key={`detail-${index}`} className={styles.coupon}>
          {item.couponName}:
          {item.deductibleAmount ? (
            <span>
              <i>-</i>
              <i></i>
              {tools.convertPenny(item.deductibleAmount)}
            </span>
          ) : (
            <div className={styles.elevenTip} onClick={handleJumpToEleven}>
              {item.activityInfo}
            </div>
          )}
        </div>
      );
    });
  }

  /**
   * 
   * from=order 
   * rf=id id
   */
  function handleJumpToEleven(): void {
    window.location.href = `${elevenActivityUrl}?from=order&rf=${Number(product.studentId) - 1}`;
  }
  /**
   * 
   */
  let packageElement = null;
  const activityAndCourse = product.courseList;
  if (Array.isArray(activityAndCourse) && activityAndCourse.length) {
    const courseList = activityAndCourse.map((course) => {
      return (
        <p key={course.code}>
          {course.name}
          <span>
            <i>x</i>
            {course.count}
            {course.unit}
          </span>
        </p>
      );
    });
    packageElement = <div className={styles.package}>{courseList}</div>;
  }

  let internalCourseElement = null;
  if (product.productType === PRODUCT_TYPE.INTERNAL_COURSE) {
    // 3999 
    internalCourseElement = (
      <div className={styles.tip}></div>
    );
  } else if (product.productType === PRODUCT_TYPE.UPGRADE_TRAIN_TO_HEGEMONY_COURSE) {
    // 
    internalCourseElement = (
      <div className={styles.tip}>11(),</div>
    );
  }

  return (
    <div className={styles.product}>
      <div className={styles.title}>
        {product.name}
        <span>{product.status === UNPAID_STATUS ? '' : ''}</span>
      </div>
      <div className={styles.content}>
        <div className={styles.name}>
          {product.name}
          <span>
            <i></i>
            {product.originalPrice}
          </span>
          <CoursePackIcon type={product.productType} />
        </div>
        {internalCourseElement}
        {packageElement}
        <hr />
        <div className={styles.order}>
          :
          <span>
            <i></i>
            {product.originalPrice}
          </span>
        </div>
        {couponElement}
      </div>
      <div className={styles.footer}>
        :<span>{product.shouldPrice}</span>
      </div>
    </div>
  );
};

export default ProductDetail;
