import { requestUtils, tools } from '../utils';
import { appConfig } from '../config';

const { apiDomain } = appConfig.domain;

//  +, IOS SPA bug
let entryUrl = window.location.href;

/**
 * URL, 
 */
export const getWechatSignature = (account: string): Promise<any> => {
  const url = `${apiDomain}/wechat/mp/jsapi/signature`;
  const data = {
    account,
    /**
     * !!! urlurl
     */
    url: tools.isIOS() ? entryUrl : window.location.href
  };
  return requestUtils.get(url, data).then((res) => res.data);
};
