import React, { FC } from 'react';

import { tools } from '@/utils';
import { useCountDown } from '@/components/hooks';
import styles from './order.less';

interface ICountDownProps {
  currentTime: string;
  endTime: string;
  className?: string;
  onFinish?: () => void;
}

/**
 * 
 */
const CountDown: FC<ICountDownProps> = ({ currentTime, endTime, onFinish, className }) => {
  const countDownTime = useCountDown(currentTime, endTime, onFinish);
  return <div className={className || styles.payTime}>{tools.formatDiffTime(countDownTime)}</div>;
};

export default CountDown;
