import React, { FC } from 'react';
import classNames from 'classnames';

import { tools } from '@/utils';
import { getFullPath } from '@/utils/tools';
import monitor from '@/utils/monitor';
import { IBaseOrder, IOrder, IOrderDetail, IActivity, ICourse } from '@/types/order';
import { ORDER_STATUS, ORDER_STATUS_TEXT, PRODUCT_TYPE } from '@/constants';
import CoursePackIcon from '@/views/components/course-pack-icon';
import OrderPayment from './order-payment';

import styles from './order.less';

interface IUnPaidOrderProps {
  orderInfo: IOrder;
  now: string;
  isPayFixed?: boolean;
  onCountDownFinish: () => void; // 
}

interface IUnpaidOrderDetailProps {
  orderInfo: IOrderDetail;
  now: string;
  isPayFixed?: boolean;
  onCountDownFinish: () => void; // 
}

/**
 * 
 */
export const UnPaidOrder: FC<IUnPaidOrderProps | IUnpaidOrderDetailProps> = ({
  orderInfo,
  now,
  isPayFixed = true,
  onCountDownFinish
}) => {
  /**
   * 
   * @param activityList 
   */
  const renderActivity = (activityList: IActivity[]) => {
    return activityList.map((item) => {
      return (
        <div key={item.id} className={styles.promotion}>
          <div className={styles.ptname}>
            <span className={styles.pattern}></span>
            {item.activityName}
            <span className={styles.price}>
              <i></i>
              {tools.convertPennyToDollar(item.price)}
            </span>
          </div>
          <div className={styles.package}>{renderPgitem(item.giveCourseList)}</div>
        </div>
      );
    });
  };

  /**
   * 
   * @param courseList 
   */
  const renderPgitem = (courseList: ICourse[]) => {
    return courseList.map((t, index) => {
      return (
        <p key={index}>
          {t.name}
          <span>
            <i>x</i>
            {t.count}
            {t.unit}
          </span>
        </p>
      );
    });
  };

  /**
   * 
   * @param actualPrice 
   * @param status 
   */
  const renderAlready = (actualPrice: number, status: ORDER_STATUS) => {
    if (actualPrice && status === ORDER_STATUS.UNPAID) {
      return (
        <div className={styles.row}>
          :
          <span className={styles.favourablePrice}>
            <i></i>
            {tools.convertPennyToDollar(orderInfo.actualPrice)}
          </span>
        </div>
      );
    }
  };

  /**
   * 
   */
  const renderFooter = () => {
    if (orderInfo.status === ORDER_STATUS.UNPAID) {
      return (
        <div className={styles.footer}>
          : <span>{tools.convertPennyToDollar(orderInfo.shouldPrice)}</span>
        </div>
      );
    } else {
      return (
        <div className={styles.footer}>
          : <span>{tools.convertPennyToDollar(orderInfo.actualPrice)}</span>
        </div>
      );
    }
  };

  const renderPayButton = () => {
    return (
      <OrderPayment
        isFixed={isPayFixed}
        orderNo={orderInfo.orderNo}
        status={orderInfo.status}
        endTime={orderInfo.expiredTime}
        currentTime={now}
        shouldPrice={orderInfo.shouldPrice}
        onCountDownFinish={onCountDownFinish}
      />
    );
  };

  let giveCourseList: ICourse[] = [];
  let activityList: IActivity[] = [];
  if ((orderInfo as IOrder).activityAndCourse) {
    giveCourseList = (orderInfo as IOrder).activityAndCourse.giveCourseList;
    activityList = (orderInfo as IOrder).activityAndCourse.activityList;
  }
  if ((orderInfo as IOrderDetail).giveCourseList) {
    giveCourseList = (orderInfo as IOrderDetail).giveCourseList;
    activityList = (orderInfo as IOrderDetail).activityList;
  }

  let internalCourseElement = null;
  if (orderInfo.productType === PRODUCT_TYPE.INTERNAL_COURSE) {
    // 3999 
    internalCourseElement = (
      <div className={styles.tip}></div>
    );
  } else if (orderInfo.productType === PRODUCT_TYPE.UPGRADE_TRAIN_TO_HEGEMONY_COURSE) {
    // 
    internalCourseElement = (
      <div className={styles.tip}>11</div>
    );
  }

  /**
   * 
   */
  let couponElement = null;
  if (orderInfo.coupons.length) {
    couponElement = orderInfo.coupons.map((item, index) => {
      return (
        <div key={`order-${index}`} className={styles.row}>
          {item.couponName}:
          {item.deductibleAmount && (
            <span className={styles.favourablePrice}>
              <i>-</i>
              <i></i>
              {tools.convertPennyToDollar(item.deductibleAmount)}
            </span>
          )}
        </div>
      );
    });
  }

  return (
    <div className={styles.order}>
      <div className={styles.title}>
        {orderInfo.name}
        <span>{ORDER_STATUS_TEXT[orderInfo.status]}</span>
      </div>
      <div className={styles.content}>
        <div className={styles.name}>
          {orderInfo.name}
          <span>
            <i></i>
            {tools.convertPennyToDollar(orderInfo.originalPrice)}
          </span>
          <CoursePackIcon type={orderInfo.productType} />
        </div>
        {internalCourseElement}
        <div className={styles.package}>{renderPgitem(giveCourseList)}</div>
        <hr className={styles.hr_solid} />
        {renderActivity(activityList)}
        <hr className={styles.hr_dotted} />
        <div className={styles.row}>
          :
          <span>
            <i></i>
            {tools.convertPennyToDollar(orderInfo.originalPrice)}
          </span>
        </div>
        {couponElement}
        <div className={styles.row}>
          :
          <span className={styles.favourablePrice}>
            <i></i>
            {tools.convertPennyToDollar(orderInfo.orderPrice)}
          </span>
        </div>
        {renderAlready(orderInfo.actualPrice as number, orderInfo.status)}
      </div>
      {renderFooter()}
      {renderPayButton()}
    </div>
  );
};

interface IFinishOderPorps {
  status: ORDER_STATUS;
  orderNo: string;
  orderName?: string;
  payTime?: string;
}

/**
 * 
 * @param orderNo 
 */
const handleJumpDetail = (orderNo: string) => {
  console.log('', orderNo);
  monitor.log({ message: `>:${orderNo}` });
  window.location.href = getFullPath('/order/' + orderNo);
};

/**
 * 
 */
export const FinishOrder: FC<IFinishOderPorps> = ({ status, orderNo, orderName, payTime }) => {
  return (
    <div className={styles.finishOrder} onClick={handleJumpDetail.bind(null, orderNo)}>
      <div className={styles.title}>
        {orderName}
        <span>{ORDER_STATUS_TEXT[status]}</span>
      </div>
      <div className={styles.paymentTime}>
        <span>{payTime}</span>
        <em className={styles.guide} />
      </div>
    </div>
  );
};

interface ISuperOrderProps {
  orderInfo: IBaseOrder;
}

/**
 * 
 */
export const SuperOrder: FC<ISuperOrderProps> = ({ orderInfo }) => {
  const orderItem = (orderList: IOrder[]) => {
    const orderListlen = orderList.length;
    return orderList.map((p, index) => {
      const classname = classNames(styles.timeline_item, {
        [styles.timeline_item_last]: orderListlen === index + 1
      });
      return (
        <li key={p.orderNo} className={classname}>
          <div className={styles.timeline_item_tail} />
          <div className={styles.timeline_item_head}>
            <div className={styles.timeline_item_hard_inner} />
          </div>
          <div
            className={styles.timeline_item_content}
            onClick={handleJumpDetail.bind(null, p.orderNo)}
          >
            <div className={styles.name}>{p.name}</div>
            <div className={styles.paymentTime}>
              <span>{p.payTime}</span>
              <em className={styles.guide} />
            </div>
          </div>
        </li>
      );
    });
  };

  return (
    <div className={styles.superOrder}>
      <div className={styles.title}>
        {orderInfo.orderName}
        <span>{ORDER_STATUS_TEXT[orderInfo.baseStatus]}</span>
      </div>
      <div className={styles.orderDetail}>
        <ul className={styles.timeline}>{orderItem(orderInfo.orderList)}</ul>
      </div>
    </div>
  );
};
