import React from 'react';
import { Route, Switch } from 'react-router-dom';

import Product from '@/views/product/product';
import ProductList from '@/views/product/product-list';
import QrCodePay from '@/views/payment/qrcode-pay';
import OrderList from '@/views/order/list';
import OrderDetail from '@/views/order/details';
import UnionPay from '@/views/payment/union-pay';
import Result from '@/views/payment/result';
import Agreement from '@/views/agreement/agreement';

const route = (
  <Switch>
    <Route key="Product" path="/product/:productId" component={Product} />,
    <Route key="ProductList" path="/product" component={ProductList} />,
    <Route key="OrderDetail" path="/order/:orderNo" component={OrderDetail} />,
    <Route key="OrderList" path="/order" component={OrderList} />,
    <Route key="QrCodePay" path="/pay/qrcode" component={QrCodePay} />,
    <Route key="UnionPay" path="/pay/unionpay" component={UnionPay} />
    <Route key="Result" path="/pay/result" component={Result} />,
    <Route key="agreement" path="/agreement" component={Agreement} />,
    <Route key="default" path="*" component={ProductList} />
  </Switch>
);

export default route;
