import React, { useState, useEffect, FC, useCallback } from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom';

import CountDown from '@/views/product/count-down';
import ProductDetail from '@/views/product/product-detail';
import PaymentButton from '@/views/payment/payment-button';

import { Loading } from '@/components/loading';
import { UserInfo } from '@/components/user/user-info';
import message from '@/utils/message';
import useCheckWechatAuth from '@/components/hooks/useCheckWechatAuth';
import { EVENT_CODE, ERROR_CODE } from '@/constants';
import useViewEvent from '@/components/hooks/useViewEvent';

import * as productService from '@/service/product-service';
import { IDetailProduct } from '@/types';
import styles from './product.less';
import { tools } from '@/utils';
import useWechatSdk from '@/components/hooks/useWechatSdk';
import { shareConfig } from '@/config';
import EmptyPage from '@/views/components/empty-page';
import monitor from '@/utils/monitor';

const defaultShareOptions = shareConfig.default;

/**
 * 
 */
const UNPAID_STATUS = 'UNPAID';

interface IUrlParams {
  /**
   * id
   */
  productId: string;
}

interface IErrorState {
  isError: boolean;
  message: string | null;
}

/**
 * 
 */
const Product: FC<RouteComponentProps<IUrlParams>> = ({ match }) => {
  const { productId } = match.params;

  const [product, setProduct] = useState<IDetailProduct | null>(null);

  const [error, updateError] = useState<IErrorState>({ isError: false, message: null });

  /**
   * 
   */
  useCheckWechatAuth({ support: 'all' });

  /**
   * JS-SDK, 
   */
  useWechatSdk({ shareStatus: true, shareOptions: defaultShareOptions });

  /**
   * 
   */
  const loadData = useCallback(
    (isInit = false) => {
      // loading, ,  message.loading
      !isInit && message.showLoading();

      /**
       * , 
       */
      productService
        .getProductDetail(productId)
        .then((product) => {
          setProduct(product);
          // close loading
          !isInit && message.closeLoading();
        })
        .catch((error) => {
          monitor.error({
            message: `, ${JSON.stringify(error)}`
          });
          const { code, msg } = error;
          switch (code) {
            case ERROR_CODE.LOGIN_REQUIRED:
              message.error('', () => {
                window.location.replace(tools.getLoginUrl());
              });
              break;
            default:
              !isInit && message.error(msg || '');
              isInit && updateError({ isError: true, message: msg || '' });
              break;
          }
        });
    },
    [productId]
  );

  /**
   * 
   */
  useEffect(() => {
    loadData(true);
  }, [productId]);

  /**
   * , 
   */
  useViewEvent({
    eventCode: EVENT_CODE.VIEW_PRODUCT_DETAIL,
    message: ''
  });

  if (error.isError) {
    return <EmptyPage describe={error.message} />;
  }

  // loading
  if (product === null) {
    return <Loading />;
  }

  let countDownElement = null;
  // 
  // && product.now && product.expiredTime
  if (product.status === UNPAID_STATUS && product.expiredTime) {
    countDownElement = (
      <>
        <div className={styles.payTip}></div>
        <CountDown endTime={product.expiredTime} currentTime={product.now} onFinish={loadData} />
      </>
    );
  }

  return (
    <div className={styles.box}>
      <UserInfo
        studentId={product.studentId}
        studentName={product.studentName}
        phone={product.phone}
      />
      <ProductDetail product={product} />
      <div className={styles.payBar}>
        {countDownElement}
        <div className={styles.pay}>
          <PaymentButton
            productId={product.productId}
            versionId={product.versionId}
            couponId={product.couponId}
            orderNo={product.orderNo}
            shouldPrice={product.shouldPrice as string}
            onReloadData={loadData}
            buttonText=""
          />
        </div>
      </div>
    </div>
  );
};

export default withRouter(Product);
