import React, { useEffect, useContext } from 'react';

import MsToast from '@/components/modal/msToast';
import { IPaymentInfo, ISelectMethod } from '@/types/payment';
import { PAYMENT_NAME } from '@/constants';
import monitor from '@/utils/monitor';
import * as ProductService from '@/service/product-service';

import { OrderContext } from '../../order/model';
import styles from '../payment.less';

/**
 *   
 * : 
 */
const useCmblifePay = (paymentInfo: IPaymentInfo, selectMethod: ISelectMethod | null) => {
  const dispatch = useContext(OrderContext);

  useEffect(() => {
    if (selectMethod === null) {
      return;
    }

    if (paymentInfo && selectMethod.paymentName === PAYMENT_NAME.CmblifeFQ) {
      let protocol = paymentInfo.payUrl; // url
      location.href = protocol;
      const content = (
        <div className={styles['modal-content']}>
          <p>1</p>
          <p>2</p>
        </div>
      );
      MsToast.modal({
        content,
        okText: '',
        onClose: handleClose,
        onOk: handleOk
      });
      monitor.log({ message: `>>,App:${protocol}` });
      console.log('- ');
    }
  }, [selectMethod]);

  /**
   * 
   */
  const handleClose = () => {
    monitor.log({ message: `>>` });
    dispatch && dispatch();
  };

  const handleOk = () => {
    // `${tools.getFullPath('/pay/result')}` + `?orderNo=${paymentInfo.orderNo}`;

    const url = ProductService.getPayResultUrl(
      PAYMENT_NAME.CmblifeFQ,
      paymentInfo.orderNo as string
    );
    monitor.log({ message: `>>,url:${url}` });
    // 
    window.location.href = url;
  };
};

export default useCmblifePay;
