import React, { FC, ReactNode, isValidElement, ReactElement } from 'react';

import rabbit from '@/public/images/common/modal-rabbit.png';
import styles from './empty-page.less';

interface IEmptyProps {
  iconUrl?: string;
  describe?: string | ReactNode;
  action?: IActionItem[];
}

interface IActionItem {
  title: string;
  onClick: () => void;
}

const renderButtons = (action?: IActionItem[]) => {
  if (!action) {
    return null;
  }
  if (!Array.isArray(action)) {
    return null;
  }
  if (!action.length) {
    return null;
  }
  return action.map((item: IActionItem, index: number) => (
    <a key={index} onClick={item.onClick} className={styles.button}>
      {item.title || ''}
    </a>
  ));
};

const EmptyPage: FC<IEmptyProps> = ({ iconUrl = rabbit, describe = '', action }) => {
  return (
    <div className={styles.empty}>
      <img src={iconUrl} alt="" />
      <div className={styles.describe}>{describe}</div>
      {renderButtons(action)}
    </div>
  );
};

export default EmptyPage;
