import { useLayoutEffect } from 'react';
import * as queryString from 'query-string';

import { ACCOUNT_TYPE } from '@/constants';
import * as tools from '@/utils/tools';
import { appConfig } from '@/config';
import * as localStore from '@/utils/local-store';

// 
const NODE_ENV = process.env.NODE_ENV;

/**
 * 
 */
interface IUseCheckWechatAuthOptions {
  account?: ACCOUNT_TYPE;
  support?: 'wechat' | 'all';
}

/**
 * url
 */
interface IUrlParam {
  from?: string;
  isappinstalled?: string;

  [propName: string]: any;
}

const defaultOptions: IUseCheckWechatAuthOptions = {
  account: ACCOUNT_TYPE.MAIN,
  support: 'wechat'
};

/**
 * 
 */
const getAuthUrl = (account: string) => {
  // tslint:disable-next-line
  let { from, isappinstalled, ...params } = queryString.parse(window.location.search) as IUrlParam;

  // URL
  let search = queryString.stringify(params);
  if (search) {
    search = `?${search}`;
  }
  const returnUrl = `${location.protocol}//${location.host}${location.pathname}${search}`;

  // 
  // : account , returnUrl 
  params = {};
  params.account = account;
  params.returnUrl = encodeURIComponent(returnUrl);

  const authUrl = `${appConfig.resources.commonAuthUrl}?${queryString.stringify(params)}`;
  return authUrl;
};

/**
 * 
 * @param options 
 */
function useCheckWechatAuth(options = defaultOptions) {
  useLayoutEffect(() => {
    // , , 
    if (NODE_ENV === 'development' || NODE_ENV === 'dev') {
      console.log('useCheckWechatAuth >>> , , ');
      return;
    }

    const account = options.account || ACCOUNT_TYPE.MAIN;
    const support = options.support || 'wechat';

    /**
     *   , 
     */
    if (!tools.isWeChat() && support === 'all') {
      console.log('useCheckWechatAuth >>>   , ');
      return;
    }

    let openIdKey;
    switch (account) {
      case ACCOUNT_TYPE.MAIN:
        // 
        openIdKey = appConfig.cookie.mainOpenIdKey;
        break;
      default:
        openIdKey = appConfig.cookie.mainOpenIdKey;
        break;
    }

    const openId = localStore.get(openIdKey) || '';

    // , 
    if (openId) {
      console.log('useCheckWechatAuth >>> , ');
      return;
    }

    const authUrl = getAuthUrl(account);
    window.location.replace(authUrl);
  }, []);
}

export default useCheckWechatAuth;
