import React, { useState, useEffect, FC, useReducer } from 'react';
import { withRouter, RouteComponentProps, Link } from 'react-router-dom';
import classNames from 'classnames';

import { shareConfig } from '@/config';
import { IPayInfo } from '@/types';
import message from '@/utils/message';
import MsToast from '@/components/modal/msToast';
import { getFullPath, getLoginUrl, convertPennyToDollar } from '@/utils/tools';

import { PAYMENT_TEXT, ORDER_STATUS, ORDER_ERROR_CODE } from '@/constants/order';
import { useWechatSdk, useStopBodyScroll } from '@/components/hooks';
import { Loading } from '@/components/loading';
import * as orderService from '@/service/order-service';
import { UnPaidOrder } from './list-item';
import { OrderContext, reducer, InitOrderState, ACTION_TYPE } from './model';
import useCheckWechatAuth from '@/components/hooks/useCheckWechatAuth';
import useViewEvent from '@/components/hooks/useViewEvent';
import { EVENT_CODE } from '@/constants';

import styles from './order.less';
import EmptyPage from '@/views/components/empty-page';
import monitor from '@/utils/monitor';

const defaultShareOptions = shareConfig.default;
const switchUrl = `/user/login?callback=/shop/order`;

interface IUrlParams {
  orderNo: string;
}

interface IErrorState {
  isError: boolean;
  message: string | null;
}

/**
 * 
 */
const OrderDetails: FC<RouteComponentProps<IUrlParams>> = ({ match }) => {
  const { orderNo } = match.params;
  const [state, dispatch] = useReducer(reducer, InitOrderState);
  const [error, updateError] = useState<IErrorState>({ isError: false, message: null });
  const [loading, updateLoading] = useState(true);
  const { orderDetail } = state;

  /**
   * 
   */
  useEffect(() => {
    loadData(true);
  }, []);

  /**
   * 
   */
  useCheckWechatAuth({ support: 'all' });

  /**
   * JS-SDK, 
   */
  useWechatSdk({ shareStatus: true, shareOptions: defaultShareOptions });

  /**
   * , 
   */
  useViewEvent({
    eventCode: EVENT_CODE.VIEW_ORDER_DETAIL,
    message: ''
  });

  /**
   * 
   * 
   */
  const loadData = async (isInit = false) => {
    try {
      !isInit && message.showLoading();
      const data = await orderService.getOrderById(orderNo);
      if (data.status === ORDER_STATUS.FINISH) {
        getStatus();
      }
      dispatch({
        type: ACTION_TYPE.UPDATE_ORDER_INFO,
        payload: {
          ...state,
          orderDetail: data
        }
      });
    } catch (error) {
      monitor.error({
        message: `, ${JSON.stringify(error)}`
      });

      // tslint:disable-next-line
      let { code, data, msg } = error || {
        code: '',
        data: {},
        msg: ''
      };
      data = data || {};
      // 
      updateError({ isError: true, message: null });
      switch (code) {
        case ORDER_ERROR_CODE.LOGIN_REQUIRED:
          message.error('', () => {
            window.location.replace(getLoginUrl());
          });
          break;
        case ORDER_ERROR_CODE.BIZ_ORDER_NO_USER_ID_NOT_MATCH:
          const content = (
            <div>
              <p style={{ marginBottom: '.2666rem' }}></p>
              <p>{data.phone}</p>
            </div>
          );
          MsToast.modal({
            content,
            closeVisible: false,
            footer: (
              <div className={styles['switch-account']}>
                <span onClick={handleSwitchAccount}></span>
              </div>
            )
          });
          break;
        case ORDER_ERROR_CODE.BIZ_ORDER_NOT_EXIST:
          MsToast.modal({
            content: '',
            closeVisible: false,
            okText: '',
            onOk: () => {
              window.location.replace(getFullPath('/order'));
            }
          });
          break;
        case ORDER_ERROR_CODE.BIZ_ORDER_INFO_ERROR:
          MsToast.modal({
            content: '',
            closeVisible: false,
            okText: '',
            onOk: () => {
              window.location.replace(getFullPath('/order'));
            }
          });
          break;
        default:
          console.log(`code: ${code}, msg: ${msg}`);
          !isInit && message.error(msg);
          isInit && updateError((state) => ({ ...state, message: msg || '' }));
          break;
      }
    } finally {
      isInit && updateLoading(false);
      !isInit && message.closeLoading();
    }
  };
  /**
   * 
   */
  const getStatus = async () => {
    try {
      const data = await orderService.protocolStatus();
      if (!data.confirm) {
        MsToast.modal({
          className: 'noTouch',
          content: (
            <div>
              <div
                className={styles['agreement']}
                dangerouslySetInnerHTML={{ __html: data.protocolHeader }}
              />
              <div
                className={styles['agreement-detail']}
                dangerouslySetInnerHTML={{ __html: data.protocolContent }}
              />
            </div>
          ),
          okText: '',
          closeText: '',
          onOk: async () => {
            try {
              await orderService.agreementConfirm();
              message.success('');
              monitor.log({
                message: '',
                eventCode: 'client_protocol_agree'
              });
            } catch (error) {
              message.error((error && error.msg) || '');
              monitor.log({
                errCode: error && error.code,
                message: ''
              });
            }
          },
          onClose: () => {
            monitor.log({
              message: '',
              eventCode: 'client_protocol_close'
            });
          }
        });
      }
    } catch (error) {
      message.error((error && error.msg) || '');
      monitor.log({
        message: error && error.msg,
        errCode: error && error.code
      });
    }
  };

  /**
   * 
   */
  const handleCountDownFinish = () => {
    loadData();
  };

  /**
   * 
   */
  const handleSwitchAccount = () => {
    window.location.replace(switchUrl);
  };

  /**
   * render
   */
  const renderOrderInfo = () => {
    if (orderDetail && orderDetail.status === ORDER_STATUS.FINISH) {
      return (
        <OrderInfo
          orderNo={orderDetail.orderNo}
          createTime={orderDetail.createTime}
          payTime={orderDetail.payTime}
          title={orderDetail.title}
          payInfos={orderDetail.payInfoList}
          agreeId={orderDetail.agreeId}
          expiredTimeNum={orderDetail.expiredTimeNum}
        />
      );
    }
  };

  // loading
  if (loading) {
    return <Loading />;
  }

  if (error.isError) {
    if (error.message) {
      return <EmptyPage describe={error.message} />;
    }
    return null;
  }

  if (!orderDetail) {
    return <EmptyPage />;
  }
  const cls = classNames(styles.box, {
    [styles.boxPaddingBm]: orderDetail.status === ORDER_STATUS.UNPAID
  });

  return (
    <OrderContext.Provider value={loadData}>
      <div className={cls}>
        <UnPaidOrder
          orderInfo={orderDetail}
          now={orderDetail.now}
          onCountDownFinish={handleCountDownFinish}
        />
        {renderOrderInfo()}
      </div>
    </OrderContext.Provider>
  );
};

export default withRouter(OrderDetails);

/*  */
interface IOrderInfoProps {
  orderNo: string;
  createTime: string;
  payTime: string;
  title: string;
  payInfos: IPayInfo[];
  agreeId: number;
  expiredTimeNum: number; // 
}

/**
 * 
 */
const OrderInfo: FC<IOrderInfoProps> = (props) => {
  const { orderNo, createTime, title, payInfos, agreeId, expiredTimeNum } = props;
  const [payDetail, setPayDetail] = useState(false);

  /**
   * 
   */
  const handleShowPayDetail = () => {
    setPayDetail(true);
  };

  /**
   * 
   */
  const handleClosePayDetail = () => {
    setPayDetail(false);
  };
  let expired = '';
  if (expiredTimeNum && expiredTimeNum > 0) {
    expired = `${expiredTimeNum}`;
  }
  return (
    <>
      <div className={styles.orderInfo}>
        <h3></h3>
        <div className={styles.row}>
          <span>{expired}</span>
        </div>
        <div className={styles.row}>
          <span>{orderNo}</span>
        </div>
        <div className={styles.row}>
          <span>{createTime}</span>
        </div>
        <div className={styles.row} onClick={handleShowPayDetail}>
          
          <span className={styles.payroll}></span>
        </div>
        <div className={styles.row}>
          
          <span className={styles.protocol}>
            <Link to={`/agreement?id=${agreeId}`}>{title}</Link>
          </span>
        </div>
      </div>
      <PayDetail showPayDetail={payDetail} onClose={handleClosePayDetail} payInfos={payInfos} />
    </>
  );
};

interface IPayDetailProps {
  showPayDetail: boolean;
  onClose: () => void;
  payInfos: IPayInfo[];
}

/**
 * 
 */
const PayDetail: FC<IPayDetailProps> = ({ showPayDetail, onClose, payInfos = [] }) => {
  let pdclassName = styles.paydetail;
  let popupmask: React.ReactNode;

  /**
   * 
   */
  useStopBodyScroll(showPayDetail);

  if (showPayDetail) {
    pdclassName = classNames(styles.paydetail, styles.poput_warp);
    popupmask = <div className={styles.popup_mask} onClick={onClose} />;
  }

  /**
   * 
   */
  const renderPayList = () => {
    return payInfos.map((item, index) => {
      return (
        <section key={index}>
          <div className={styles.row}>
            <span>{item.subPayTime}</span>
          </div>
          <div className={styles.row}>
            <span>{PAYMENT_TEXT[item.subPayment]}</span>
          </div>
          <div className={styles.row}>
            
            <span>
              <i></i>
              {convertPennyToDollar(item.subActualPrice)}
            </span>
          </div>
        </section>
      );
    });
  };

  return (
    <>
      {popupmask}
      <div className={pdclassName}>
        <header>
          
          <span onClick={onClose}></span>
        </header>
        <div className={styles.content}>{renderPayList()}</div>
      </div>
    </>
  );
};
