import React, { FC } from 'react';

import { PRODUCT_TYPE } from '@/constants/common';
import baseIcon from '@/public/images/product/base.png';
import gift from '@/public/images/product/gift.png';
import advanced from '@/public/images/product/advanced.png';
import excellentIcon from '@/public/images/product/excellent.png';

interface ICoursePackIconProps {
  type?: PRODUCT_TYPE;
}

/**
 * icon
 * @param type 
 */
const CoursePackIcon: FC<ICoursePackIconProps> = (props) => {
  const { type } = props;
  let iconUrl = baseIcon; // icon
  switch (type) {
    case PRODUCT_TYPE.YEAR_COURSE: // 
    case PRODUCT_TYPE.TRAIN_RISE_BASE_COURSE: //  
    case PRODUCT_TYPE.INTERNAL_RISE_BASE_COURSE: // 3999 
      iconUrl = baseIcon;
      break;
    case PRODUCT_TYPE.TRAIN_COURSE:
    case PRODUCT_TYPE.SEASON_COURSE:
    case PRODUCT_TYPE.INTERNAL_COURSE:
      iconUrl = gift; // icon
      break;
    case PRODUCT_TYPE.UPGRADE_BIG_COURSE:
    case PRODUCT_TYPE.TWO_YEARS_COURSE:
    case PRODUCT_TYPE.UNOBSTRUCTED_LEARNING365:
    case PRODUCT_TYPE.UPGRADE_365_TO_TWO_YEAR_COURSE:
      iconUrl = advanced; // icon
      break;
    case PRODUCT_TYPE.UPGRADE_THREE_YEAR_COURSE:
    case PRODUCT_TYPE.THREE_YEARS_COURSE:
    case PRODUCT_TYPE.UPGRADE_TRAIN_TO_HEGEMONY_COURSE:
      iconUrl = excellentIcon; // icon
      break;
  }
  return <img src={iconUrl} />;
};

export default CoursePackIcon;
