/**
 * 
 */
export const getProductList = (): Promise<{ list: IListProduct[]; now: string }> => {
  const url = apiDomain.concat('/usercenter/order/pay/product/info');
  return requestUtils.get(url).then((res) => {
    return {
      list: res.data.productInfos as IListProduct[],
      now: res.data.now as string
    };
  });
};

/**
 * 
 *    2019101,  
 * 3999, 
 * @param product 
 */
const processOrderDetail = (product: IDetailProduct) => {
  // 2019101
  const flagTime = dayjs('2019-10-01 00:00:00');
  // 
  const createTime = dayjs(product.createTime);

  //    
  if (isNaN(createTime.valueOf()) || createTime > flagTime) {
    /**
     * 
     * 3999, 
     */
    let classGroup: CourseInfo | null = {
      code: 'CLASS_GROUP',
      name: '',
      count: '2',
      unit: ''
    };

    switch (product.productType) {
      case PRODUCT_TYPE.INTERNAL_COURSE: // 3999  6
        classGroup.count = '6';
        break;
      case PRODUCT_TYPE.INTERNAL_RISE_BASE_COURSE: // 3999  2
        classGroup.count = '2';
        break;
      case PRODUCT_TYPE.TRAIN_RISE_BASE_COURSE: //  8
      case PRODUCT_TYPE.YEAR_COURSE: //   8
      case PRODUCT_TYPE.UPGRADE_BIG_COURSE: //   8
        classGroup.count = '8';
        break;
      case PRODUCT_TYPE.TWO_YEARS_COURSE: //   16
      case PRODUCT_TYPE.UPGRADE_THREE_YEAR_COURSE: //   16
        classGroup.count = '16';
        break;
      case PRODUCT_TYPE.UPGRADE_TRAIN_TO_HEGEMONY_COURSE: //   32
      case PRODUCT_TYPE.THREE_YEARS_COURSE: //   32
        classGroup.count = '32';
        break;
      default:
        classGroup = null;
        break;
    }

    if (classGroup) {
      if (!Array.isArray(product.courseList)) {
        product.courseList = [];
      }
      product.courseList.push(classGroup);
    }
  }
};

/**
 * Id 
 * @param productId id
 */
export const getProductDetail = (productId: string): Promise<IDetailProduct> => {
  const url = apiDomain.concat('/usercenter/order/product/detail');
  return requestUtils.get(url, { productId }).then((res) => {
    const product = res.data as IDetailProduct;
    // 
    product.name = product.name.replace(/|/g, '');
    product.originalPrice = tools.convertPenny(product.originalPrice as number);
    // ,  
    product.shouldPrice = tools.convertPenny(product.shouldPrice as number);
    // product.couponPrice = tools.convertPenny(product.couponPrice as number);

    //    2019101,  
    processOrderDetail(product);

    return product;
  });
};

