/**
 *  
 * @param params 
 */
export const getPaymentMethod = (params: IGetPaymentMethodParams): Promise<IPaymentMethod> => {
  const url = apiDomain.concat('/usercenter/order/pay/order/info');
  return requestUtils.get(url, params).then((res) => {
    return handlePaymentMethod(res.data);
  });
};

/**
 * 
 * 1. PC,  
 * 2. H5, ,  
 * 3. H5, ,  h5
 * 4. , 
 */
const handlePaymentMethod = (payment: IPaymentMethod): IPaymentMethod => {
  const paymentMethodList: IPaymentName[] = [];

  // , 
  if (tools.isWeChat()) {
    // 
    paymentMethodList.push({
      paymentName: PAYMENT_NAME.WechatMP,
      paymentText: PAYMENT_NAME_TEXT[PAYMENT_NAME.WechatMP],
      noInterest: false, // , : 00 ,  : 
      orderStages: [],
      stageType: 'no' // 
    });
  } else {
    // H5
    if (tools.isWap()) {
      // h5
      paymentMethodList.push({
        paymentName: PAYMENT_NAME.AlipayWap,
        paymentText: PAYMENT_NAME_TEXT[PAYMENT_NAME.AlipayWap],
        noInterest: false, // , : 00 ,  : 
        orderStages: [],
        stageType: 'no' // 
      });

      // 
      paymentMethodList.push(...payment.paymentList);
    } else {
      // pc
      // 
      paymentMethodList.push({
        paymentName: PAYMENT_NAME.WechatQR,
        paymentText: PAYMENT_NAME_TEXT[PAYMENT_NAME.WechatQR],
        noInterest: false, // , : 00 ,  : 
        orderStages: [], // 
        stageType: 'no'
      });
      // 
      paymentMethodList.push({
        paymentName: PAYMENT_NAME.AlipayQR,
        paymentText: PAYMENT_NAME_TEXT[PAYMENT_NAME.AlipayQR],
        noInterest: false, // , : 00 ,  : 
        orderStages: [],
        stageType: 'no' // 
      });
    }
  }

  // 
  payment.paymentList = paymentMethodList;

  payment.paymentList.forEach((item) => {
    // 
    if (Array.isArray(item.orderStages) && item.orderStages.length) {
      item.stageType = 'close';
    } else {
      item.stageType = 'no';
    }
    // 
    item.paymentText = PAYMENT_NAME_TEXT[item.paymentName];
  });

  return payment;
};

