/**
 *  
 * appConfig.resources.payInfoCallbackUrl : 
 */
export const getPaymentInfo = (params: ISelectMethod): Promise<IPaymentInfo> => {
  // 
  params.callbackUrl = appConfig.resources.payInfoCallbackUrl;

  const url = apiDomain.concat('/usercenter/order/pay/info');
  return requestUtils.get(url, params, { trackInOut: true }).then(
    (res): IPaymentInfo => {
      //  
      const payInfo = res.data as IPaymentInfo;

      /**
       * url, 
       * 1. , payInfo.orderNo 
       * 2. , payInfo.orderNo 
       */
      payInfo.payResultUrl = getPayResultUrl(params.paymentName, payInfo.orderNo as string);
      return payInfo;
    }
  );
};

/**
 *   
 * @param params 
 */
export const getQRCodePaymentInfo = async (params: ISelectMethod): Promise<IQRCodePaymentInfo> => {
  return Promise.all<IPaymentMethod, IPaymentInfo>([
    getPaymentMethod({ productId: params.productId, orderNo: params.orderNo }),
    getPaymentInfo(params)
  ]).then(([paymentMethod, paymentInfo]) => {
    // 
    const qrCodePaymentInfo: IQRCodePaymentInfo = {
      name: paymentMethod.name,
      paymentPrice: paymentMethod.paymentPrice,
      ...paymentInfo
    };

    return qrCodePaymentInfo;
  });
};

