export const getWechatPaymentInfo = (params: ISelectMethod): Promise<IWechatPayOptions> => {
  const url = apiDomain.concat('/usercenter/order/pay/info');
  return requestUtils.get(url, params, { trackInOut: true }).then(
    (res): IWechatPayOptions => {
      //   
      const payInfo = res.data as IWechatPaymentInfo;

      return {
        appId: payInfo.appId,
        nonceStr: payInfo.nonceStr,
        orderNo: payInfo.orderNo,
        /**
         * , , (java package , )
         */
        package: payInfo.packages,
        paySign: payInfo.paySign,
        signType: payInfo.signType,
        /**
         * , 
         */
        timestamp: payInfo.timeStamp,
        /**
         * url, 
         */
        payResultUrl: getPayResultUrl(params.paymentName, payInfo.orderNo)
      };
    }
  );
};

