/**
 *  paymentName  channel  type
 * @param paymentName 
 */
const getChannelAndType = (
  paymentName: PAYMENT_NAME
): { channel: PAYMENT_CHANNEL; type: PAYMENT_TYPE } => {
  switch (paymentName.toLowerCase()) {
    case PAYMENT_NAME.AlipayWap.toLowerCase(): // wap
      return {
        channel: PAYMENT_CHANNEL.ALIPAY,
        type: PAYMENT_TYPE.WAP
      };
      break;
    case PAYMENT_NAME.AlipayHB.toLowerCase(): // 
      return {
        channel: PAYMENT_CHANNEL.ALIPAY,
        type: PAYMENT_TYPE.INSTALLMENT
      };
      break;
    case PAYMENT_NAME.AlipayQR.toLowerCase(): // 
      return {
        channel: PAYMENT_CHANNEL.ALIPAY,
        type: PAYMENT_TYPE.QRCODE
      };
      break;

    case PAYMENT_NAME.WechatMP.toLowerCase(): // 
      return {
        channel: PAYMENT_CHANNEL.WECHAT,
        type: PAYMENT_TYPE.MP
      };
      break;
    case PAYMENT_NAME.WechatQR.toLowerCase(): // 
      return {
        channel: PAYMENT_CHANNEL.WECHAT,
        type: PAYMENT_TYPE.QRCODE
      };
      break;

    case PAYMENT_NAME.CmblifeFQ.toLowerCase(): // 
      return {
        channel: PAYMENT_CHANNEL.CMBLIFE,
        type: PAYMENT_TYPE.INSTALLMENT
      };
      break;
    case PAYMENT_NAME.CCB_BLANK_FQ.toLowerCase(): // 
      return {
        channel: PAYMENT_CHANNEL.CCB_BLANK,
        type: PAYMENT_TYPE.INSTALLMENT
      };
      break;
    case PAYMENT_NAME.GNETPG_FQ.toLowerCase(): // 
      return {
        channel: PAYMENT_CHANNEL.GNETPG,
        type: PAYMENT_TYPE.INSTALLMENT
      };
      break;

    case PAYMENT_NAME.UNIONPAY_BOC_FQ.toLowerCase(): // 
      return {
        channel: PAYMENT_CHANNEL.UNIONPAY_BOC,
        type: PAYMENT_TYPE.INSTALLMENT
      };
      break;

    case PAYMENT_NAME.UNIONPAY_ABOC_FQ.toLowerCase(): // 
      return {
        channel: PAYMENT_CHANNEL.UNIONPAY_ABOC,
        type: PAYMENT_TYPE.INSTALLMENT
      };
      break;

    case PAYMENT_NAME.UNIONPAY_HXB_FQ.toLowerCase(): //  
      return {
        channel: PAYMENT_CHANNEL.UNIONPAY_HXB,
        type: PAYMENT_TYPE.INSTALLMENT
      };
      break;

    case PAYMENT_NAME.UNIONPAY_SPDB_FQ.toLowerCase(): // 
      return {
        channel: PAYMENT_CHANNEL.UNIONPAY_SPDB,
        type: PAYMENT_TYPE.INSTALLMENT
      };
      break;

    case PAYMENT_NAME.UNIONPAY_CMBC_FQ.toLowerCase(): // 
      return {
        channel: PAYMENT_CHANNEL.UNIONPAY_CMBC,
        type: PAYMENT_TYPE.INSTALLMENT
      };
      break;
    default:
      throw new Error('');
      break;
  }
};

/**
 * 
 * @param paymentName 
 * @param orderNo 
 */
export const getPayResultUrl = (paymentName: PAYMENT_NAME, orderNo: string) => {
  // ,  : /pay/pay_result, : /pay/result
  if (appConfig.resources.payResultPath === '/pay/pay_result') {
    // 
    //  channel  type
    const { channel, type } = getChannelAndType(paymentName);

    const url =
      `${appConfig.resources.websiteDomain + appConfig.resources.payResultPath}?` +
      `channel=${channel}` +
      `&type=${type}` +
      `&order_no=${orderNo}` +
      `&callback_url=${encodeURIComponent(appConfig.resources.payInfoCallbackUrl)}`;
    return url;
  } else {
    // 
    const url = `${tools.getFullPath('/pay/result')}` + `?orderNo=${orderNo}`;
    return url;
  }
};
