import React, { FC, useEffect, useState } from 'react';
import queryString from 'query-string';
import QRCode from 'qrcode.react';

import { Loading } from '@/components/loading';
import * as productService from '@/service/product-service';
import { IOrderStatus, IQRCodePaymentInfo } from '@/types/payment';
import message from '@/utils/message';
import { ERROR_CODE, EVENT_CODE, ORDER_PAY_STATUS, PAYMENT_NAME } from '@/constants';
import { tools } from '@/utils';
import useCheckWechatAuth from '@/components/hooks/useCheckWechatAuth';
import msToast from '@/components/modal/msToast';
import useViewEvent from '@/components/hooks/useViewEvent';

import styles from './qrcode-pay.less';
import { shareConfig } from '@/config';
import useWechatSdk from '@/components/hooks/useWechatSdk';
import monitor from '@/utils/monitor';
import EmptyPage from '@/views/components/empty-page';

const defaultShareOptions = shareConfig.default;

/**
 * setInterval 
 */
const delay = 4000;

/**
 * url 
 */
interface IUrlParams {
  /**
   * id, 
   */
  productId: string;

  /**
   * , 
   */
  orderNo: string;

  /**
   * , 
   */
  method: PAYMENT_NAME;
}

interface IErrorState {
  isError: boolean;
  message: string | null;
}

/**
 * 
 */
const QrCodePay: FC = () => {
  // 
  const [paymentInfo, setPaymentInfo] = useState<IQRCodePaymentInfo | null>(null);
  // 
  const [dataUrl, setDataUrl] = useState('');
  // 
  const [error, updateError] = useState<IErrorState>({ isError: false, message: null });

  /**
   * 
   */
  useCheckWechatAuth({ support: 'all' });

  /**
   * JS-SDK, 
   */
  useWechatSdk({ shareStatus: true, shareOptions: defaultShareOptions });

  /**
   * 
   */
  useEffect(() => {
    //  url  id, ?, ?, 
    const { productId, orderNo, method } = queryString.parse(window.location.search) as IUrlParams;

    productService
      .getQRCodePaymentInfo({ productId, orderNo, paymentName: method })
      .then((paymentInfo) => {
        // 
        monitor.log({
          message: `: ${method}, `
        });
        setPaymentInfo(paymentInfo);
      })
      .catch((error) => {
        const { code, msg } = error;

        // 
        monitor.error({
          message: `: ${method}, , code: ${JSON.stringify(error)}`
        });
        // 
        updateError({ isError: true, message: null });
        switch (code) {
          case ERROR_CODE.LOGIN_REQUIRED:
            // message.error('', () => {
            //   window.location.replace(tools.getLoginUrl());
            // });
            msToast.modal({
              content: '',
              closeVisible: false,
              okText: '',
              onOk: () => {
                window.location.replace(tools.getLoginUrl());
              }
            });
            break;
          case ERROR_CODE.BIZ_ORDER_NOT_EXIST: // 
          case ERROR_CODE.BIZ_ORDER_IS_PAID: // 
          case ERROR_CODE.BIZ_ORDER_IS_PAID_SUB: // 
          case ERROR_CODE.BIZ_ORDER_IS_REFUND: // 
          case ERROR_CODE.BIZ_ORDER_EXPIRYTIME_OVER: // 
          case ERROR_CODE.BIZ_ORDER_IS_CLOSE: // 
          case ERROR_CODE.BIZ_ORDER_IS_SUB: // 
          case ERROR_CODE.BIZ_ORDER_NO_USER_ID_NOT_MATCH: // ID
          case ERROR_CODE.BIZ_ORDER_STATUS_IS_ERROR: // 
            msToast.modal({
              content: msg || '',
              closeVisible: false,
              okText: '',
              onOk: () => {
                window.location.replace(tools.getFullPath('/order'));
              }
            });
            break;
          default:
            updateError({ isError: true, message: msg || '' });
            break;
        }
      });
  }, []);

  /**
   * , , : 4
   */
  useEffect(() => {
    if (!paymentInfo) {
      return;
    }

    const timer = setInterval(() => {
      productService
        .queryOrderStatus({
          orderNo: paymentInfo.orderNo,
          paymentName: paymentInfo.paymentName
        })
        .then((orderStatus: IOrderStatus) => {
          // TODO 
          switch (orderStatus.status) {
            /**
             * 
             */
            case ORDER_PAY_STATUS.BIZ_ORDER_NOT_PAID:
              console.log(', ...');
              break;
            /**
             * 
             */
            case ORDER_PAY_STATUS.BIZ_ORDER_ALREADY_PAID:
              // 
              monitor.log({
                message: `: `
              });
              // 
              clearInterval(timer);
              // 
              window.location.replace(paymentInfo.payResultUrl as string);
              break;
            /**
             * 
             */
            case ORDER_PAY_STATUS.LOGIN_REQUIRED:
              clearInterval(timer);
              message.error('', () => {
                window.location.replace(tools.getLoginUrl());
              });
              break;
            /**
             * 
             */
            default:
              clearInterval(timer);
              message.error(orderStatus.statusText || '');
              break;
          }
        })
        .catch((error) => {
          // 
          monitor.error({
            message: `: : ${JSON.stringify(error)}`
          });
          // 
          clearInterval(timer);
          message.error((error && error.msg) || '');
        });
    }, delay);

    return () => {
      clearInterval(timer);
    };
  }, [paymentInfo]);

  /**
   *  canvas to base64
   */
  useEffect(() => {
    if (!paymentInfo) {
      return;
    }
    const canvas = window.document.getElementsByTagName('canvas')[0];
    if (!canvas) {
      return;
    }
    const imgDataUrl = canvas.toDataURL('image/png');
    setDataUrl(imgDataUrl);
  }, [paymentInfo]);

  /**
   * , 
   */
  useViewEvent({
    eventCode: EVENT_CODE.VIEW_QRCODE_PAY,
    message: ''
  });

  // 
  let imgElement = null;
  if (dataUrl) {
    imgElement = (
      <>
        <img src={dataUrl} alt="" />
        <div className={styles.desc}>()</div>
      </>
    );
  }

  let headerElement = null; // 
  let headerClass = ''; // 
  if (paymentInfo) {
    console.log('paymentInfo', paymentInfo);
    let type = null;
    switch (paymentInfo.paymentName.toLowerCase()) {
      case PAYMENT_NAME.WechatQR.toLowerCase():
        type = <span></span>;
        headerClass = styles.wechat;
        break;
      case PAYMENT_NAME.AlipayQR.toLowerCase():
        type = <span></span>;
        headerClass = styles.alipay;
        break;
    }
    headerElement = <header className={headerClass}>{type}</header>;
  }

  if (error.isError) {
    if (error.message) {
      return <EmptyPage describe={error.message} />;
    }
    return null;
  }

  // loading
  if (paymentInfo === null) {
    return <Loading />;
  }

  return (
    <div className={styles.box}>
      <div className={styles.pay}>
        {headerElement}
        <div className={styles.price}>
          <i></i>
          {tools.convertPenny(paymentInfo.paymentPrice)}
        </div>
        <p>
          
          <span>{paymentInfo.name}</span>
        </p>
        <p>
          
          <span>{paymentInfo.orderNo}</span>
        </p>
        <p>
          
          <span></span>
        </p>
        <hr />
        <div className={styles.qrBox}>
          {imgElement}
          {paymentInfo.payUrl ? <QRCode value={paymentInfo.payUrl} level={'H'} /> : null}
        </div>
      </div>
    </div>
  );
};

export default QrCodePay;
