import logHelper from '@mmears/log';

import * as localStore from './local-store';

const logger = logHelper({
  eventName: 'shop_log',
  showLog: false
});

interface ILogOptions {
  /**
   * ,  'OK'
   */
  errCode?: string;
  /**
   * 
   */
  eventCode?: string;
  /**
   * message
   */
  message: string;
  /**
   * 
   */
  account?: string;
}

/**
 * , 
 */
const monitor = {
  /**
   * 
   * @param userId
   */
  login(userId: string) {
    logger.login(userId);
  },
  /**
   *  logout id
   */
  logout() {
    logger.logout();
  },
  /**
   * 
   * @param options 
   */
  log: (options: ILogOptions) => {
    logger.info({
      errCode: options.errCode || 'OK',
      eventCode: options.eventCode || '',
      account:
        localStore.getUserId() || (localStore.getWechatToken() && '') || '',
      message: options.message
    });
  },
  /**
   * 
   * @param options 
   */
  error: (options: ILogOptions) => {
    logger.info({
      errCode: options.errCode || 'ERROR',
      eventCode: options.eventCode || '',
      account:
        localStore.getUserId() || (localStore.getWechatToken() && '') || '',
      message: options.message
    });
    console.error(options);
  }
};

export default monitor;
