import { useEffect } from 'react';

import { IPaymentInfo, ISelectMethod } from '@/types/payment';
import { PAYMENT_NAME } from '@/constants';

/**
 * url, :   
 * URLH5
 * :  
 * @param paymentInfo paymentInfo
 * @param selectMethod 
 */
const useUrlPay = (paymentInfo: IPaymentInfo, selectMethod: ISelectMethod | null) => {
  useEffect(() => {
    if (selectMethod === null) {
      return;
    }
    //   
    // 
    if (
      selectMethod.paymentName === PAYMENT_NAME.CCB_BLANK_FQ ||
      selectMethod.paymentName === PAYMENT_NAME.GNETPG_FQ
    ) {
      window.location.href = paymentInfo.payUrl;
    }
  }, [selectMethod]);
};

export default useUrlPay;
