import React, { FC, useEffect, useReducer, useState } from 'react';
import { withRouter } from 'react-router-dom';

import { shareConfig } from '@/config';
import * as orderService from '@/service/order-service';
import { ORDER_STATUS, EVENT_CODE } from '@/constants';
import { Loading } from '@/components/loading';
import { useWechatSdk } from '@/components/hooks';
import { tools } from '@/utils';
import { OrderContext, reducer, InitOrderState, ACTION_TYPE } from './model';
import { FinishOrder, SuperOrder, UnPaidOrder } from './list-item';
import EmptyPage from '../components/empty-page';
import useCheckWechatAuth from '@/components/hooks/useCheckWechatAuth';
import useViewEvent from '@/components/hooks/useViewEvent';

import styles from './order.less';
import message from '@/utils/message';
import monitor from '@/utils/monitor';

const defaultShareOptions = shareConfig.default;

interface IErrorState {
  isError: boolean;
  message: string | null;
}

/**
 * 
 */
const OrderList: FC = () => {
  const [state, dispatch] = useReducer(reducer, InitOrderState);
  const [loading, setLoading] = useState(true);
  const [error, updateError] = useState<IErrorState>({ isError: false, message: null });
  const { orderList } = state;

  useEffect(() => {
    loadOrderList(true);
  }, []);

  /**
   * 
   */
  useCheckWechatAuth({ support: 'all' });

  /**
   * JS-SDK, 
   */
  useWechatSdk({ shareStatus: true, shareOptions: defaultShareOptions });

  /**
   * , 
   */
  useViewEvent({
    eventCode: EVENT_CODE.VIEW_ORDER_LIST,
    message: ''
  });

  /**
   * 
   */
  const loadOrderList = async (isInit = false) => {
    try {
      !isInit && message.showLoading();
      const data = await orderService.getOrderList();
      dispatch({
        type: ACTION_TYPE.UPDATE_ORDER_LIST,
        payload: {
          ...state,
          orderList: data
        }
      });
    } catch (error) {
      monitor.error({
        message: `, ${JSON.stringify(error)}`
      });
      const { code, msg } = error;
      switch (code) {
        case 'LOGIN_REQUIRED':
          message.error('', () => {
            window.location.replace(tools.getLoginUrl());
          });
          break;
        default:
          console.log(`code: ${code}, msg: ${msg}`);
          !isInit && message.error(msg);
          isInit && updateError({ isError: true, message: msg || '' });
          break;
      }
    } finally {
      isInit && setLoading(false);
      !isInit && message.closeLoading();
    }
  };
  /**
   * 
   */
  const handleCountDownFinish = () => {
    loadOrderList();
  };

  /**
   * 
   */
  const renderOrderList = () => {
    if (!orderList) {
      return;
    }
    return orderList.map((item) => {
      switch (item.baseStatus) {
        case ORDER_STATUS.UNPAID:
          // 
          return item.orderList.map((order) => {
            return (
              <UnPaidOrder
                isPayFixed={false}
                key={order.orderNo}
                orderInfo={order}
                now={item.now}
                onCountDownFinish={handleCountDownFinish}
              />
            );
          });
        default:
          // orderList >1 /
          return item.orderList && item.orderList.length > 1 ? (
            <SuperOrder key={item.orderNo} orderInfo={item} />
          ) : (
            <FinishOrder
              key={item.orderNo}
              orderNo={item.orderNo}
              orderName={item.orderName}
              status={item.baseStatus}
              payTime={item.paidTime}
            />
          );
      }
    });
  };

  // useVisibilityChange(
  //   function() {
  //     console.log('', Date.now());
  //   },
  //   function() {
  //     console.log('', Date.now());
  //   }
  // );
  if (loading) {
    return <Loading />;
  }

  if (error.isError) {
    return <EmptyPage describe={error.message} />;
  }

  if (!orderList || !orderList.length) {
    return <EmptyPage describe={''} />;
  }

  return (
    <OrderContext.Provider value={loadOrderList}>
      <div className={styles.box}>{renderOrderList()}</div>
    </OrderContext.Provider>
  );
};

export default withRouter(OrderList);
