import React, { FC, SyntheticEvent, useCallback, useContext, useState } from 'react';
import { Button, InputItem, List } from 'antd-mobile';
import { ERROR_CODE, UNION_BANK_CARD } from '@/constants';
import styles from './union-pay.less';
import { ACTION_TYPE, Context, IUnionPayState, PAGE_TYPE } from './model';
import message from '@/utils/message';
import { queryUnionPayOpenStatus } from '@/service/product-service';
import { tools } from '@/utils';
import msToast from '@/components/modal/msToast';
import monitor from '@/utils/monitor';

/**
 * antd-mobileinput4
 */
const BANK_INPUT_MAX_LENGTH = UNION_BANK_CARD.MAX_LENGTH + ~~(UNION_BANK_CARD.MAX_LENGTH / 4);

/**
 * 
 */
const bankPattern = /^([1-9]{1})(\d{12,18})$/;

/**
 * 
 * @param bankNo 
 */
const verifyBankCard = (bankNo?: string): string => {
  if (!bankNo) {
    message.error('~');
    return '';
  }
  const bankStr = bankNo.replace(/\s+/g, '');
  if (!bankPattern.test(bankStr)) {
    message.error('');
    return '';
  }
  return bankStr;
};

/**
 * form
 * @param form form
 */
const handleForm = (form?: string) => {
  if (!form) {
    message.error('');
    return;
  }
  monitor.log({ message: '' });
  const div = document.createElement('div');
  div.innerHTML = decodeURIComponent(form);
  document.body.appendChild(div);

  // 
  const forms = div.childNodes;
  const $form = Array.prototype.find.call(forms, (item: { id: string }) => item.id === 'pay_form');
  // 
  $form && $form.submit && $form.submit();
};

/**
 * 
 * ref https://open.unionpay.com/tjweb/acproduct/list?apiservId=449
 */
const BankCard: FC = () => {
  const {
    dispatch,
    pageType,
    orderNo,
    stageCount,
    bankNo,
    paymentName
  }: IUnionPayState = useContext(Context);

  //   disabled
  const [disabled, updateDisabled] = useState<boolean>(!!bankNo);
  //   loading
  const [loading, updateLoading] = useState<boolean>(false);

  /**
   * inputchang
   */
  const handleBankChange = useCallback((value: string): void => {
    if (bankPattern.test(value.replace(/\s+/g, ''))) {
      updateDisabled(false);
    } else {
      updateDisabled(true);
    }
    if (!dispatch) {
      return;
    }
    dispatch({
      type: ACTION_TYPE.UPDATE_BANK_NO,
      payload: {
        bankNo: value
      }
    });
  }, []);

  /**
   *  
   */
  const handlePayClick = useCallback(
    async (e: SyntheticEvent): Promise<undefined> => {
      e.preventDefault();
      // 
      const bankStr = verifyBankCard(bankNo);
      if (!bankStr) {
        monitor.log({ message: ` => bankNo: ${bankNo}; bankStr: ${bankStr}` });
        return;
      }
      try {
        updateDisabled(true);
        updateLoading(true);
        // 
        if (!orderNo || !stageCount || !paymentName) {
          monitor.log({
            message: `[API] => ${JSON.stringify({
              stageCount,
              paymentName
            })}`
          });
          message.error('');
          return;
        }
        monitor.log({
          message: `[API] => ${JSON.stringify({
            orderNo,
            bankNo: bankStr,
            stageCount,
            paymentName
          })}`
        });
        const result = await queryUnionPayOpenStatus({
          orderNo,
          bankNo: bankStr,
          stageCount,
          paymentName
        });
        monitor.log({
          message: `[API],  => ${JSON.stringify(result)}`
        });
        if (result && result.form) {
          // 
          // 
          handleForm(result.form);
          // msToast.modal({
          //   content: '',
          //   onOk: () => {
          //     handleForm(result.form);
          //   },
          //   okText: '',
          //   closeText: ''
          // });
          return;
        }
        // // 
        dispatch &&
          dispatch({
            type: ACTION_TYPE.UPDATE_ORDER_INFO,
            payload: {
              // ()
              orderNo: result.orderNo,
              // 
              phoneNo: result.phoneNo,
              // 
              pageType: PAGE_TYPE.SMS_CODE
            }
          });
      } catch (error) {
        monitor.error({
          message: `[API] => ${JSON.stringify(error)}`
        });
        const { code, msg } = error;
        switch (code) {
          case ERROR_CODE.LOGIN_REQUIRED:
            // message.error('', () => {
            //   window.location.replace(tools.getLoginUrl());
            // });
            msToast.modal({
              content: '',
              closeVisible: false,
              okText: '',
              onOk: () => {
                window.location.replace(tools.getLoginUrl());
              }
            });
            break;
          case ERROR_CODE.BIZ_ORDER_NOT_EXIST: // 
          case ERROR_CODE.BIZ_ORDER_IS_REFUND: // 
          case ERROR_CODE.BIZ_ORDER_EXPIRYTIME_OVER: // 
          case ERROR_CODE.BIZ_ORDER_IS_CLOSE: // 
          case ERROR_CODE.BIZ_ORDER_IS_PAID: // 
          case ERROR_CODE.BIZ_ORDER_IS_PAID_SUB: // 
          case ERROR_CODE.BIZ_ORDER_IS_SUB: // 
          case ERROR_CODE.BIZ_ORDER_NO_USER_ID_NOT_MATCH: // ID
          case ERROR_CODE.BIZ_ORDER_STATUS_IS_ERROR: // 
            msToast.modal({
              content: msg || '',
              closeVisible: false,
              okText: '',
              onOk: () => {
                window.location.replace(tools.getFullPath('/order'));
              }
            });
            break;
          default:
            msToast.modal({
              content: msg || '',
              closeVisible: false,
              okText: ''
            });
            break;
        }
      } finally {
        updateLoading(false);
        updateDisabled(false);
      }
    },
    [bankNo]
  );

  if (pageType !== PAGE_TYPE.BANK) {
    return null;
  }

  return (
    <div className={styles.bankCardContainer}>
      <List>
        <InputItem
          type="bankCard"
          placeholder="~"
          prefixListCls="bank"
          maxLength={BANK_INPUT_MAX_LENGTH}
          onChange={handleBankChange}
          value={bankNo}
        />
      </List>
      <Button className={styles.btn} type="primary" disabled={disabled} onClick={handlePayClick}>
        {(loading && '...') || ''}
      </Button>
    </div>
  );
};

export default BankCard;
