import RequestUtils, {
  IAnyValueObject,
  IRequestConfig,
  IRequestGlobalConfig
} from '@mmears/request';
import logHelper from '@mmears/log';

import * as localStore from '@/utils/local-store';

/**
 * 
 */
const logger = logHelper({
  eventName: 'shop_log',
  showLog: false,
  getAccount: () => {
    return localStore.getUserId() || '';
  }
});

/**
 *   
 */
const config: IRequestGlobalConfig = {
  /**
   * ,  'OK'
   */
  successCode: 'OK',
  /**
   * cookie
   */
  withCredentials: true,
  logger,
  perfRule: 'all', //  
  /**
   * 
   */
  headersHandle: () => {
    const headers = {
      'X-USER-TOKEN': localStore.getToken() // token
    };

    if (process.env.NODE_ENV === 'development') {
      headers[
        'X-USER-TOKEN'
      ] = `AJxReiDwBnQSYUaOkH5bw2pXR7ibiBoYCA9BMsDfKug4hOCPR6AP1btf5c5Blc1VV9EIync6ubaob1H3l98eLwQ9Zmrcq4761n1C7TaLE37GtIZHXN68sDHt5rGGdm6sJzTmvjb7b3fQCUTKbxI0dA`;
    }

    //  H5, wxToken, 
    const wechatToken = localStore.getWechatToken();
    if (wechatToken) {
      // @ts-ignore
      headers['X-WECHAT-TOKEN'] = wechatToken;
    }

    return headers;
  }
};

/**
 * 
 */
const request = new RequestUtils(config);

/**
 * 
 * @returns {*}
 */
const fetchUtils = {
  /**
   * 
   * @param url url
   * @param params queryString 
   * @param config 
   */
  get(url: string, params?: IAnyValueObject, config?: IRequestConfig) {
    return request.get(url, params, config);
  },
  /**
   * 
   * @param url url
   * @param data 
   * @param config 
   */
  post(url: string, data?: any, config?: IRequestConfig) {
    return request.post(url, data, config);
  },
  /**
   * 
   * @param url url
   * @param data 
   * @param config 
   */
  postJson(url: string, data?: any, config?: IRequestConfig) {
    return request.postJson(url, data, config);
  }
};

export default fetchUtils;
