import React, { FC, useEffect, useState } from 'react';
import queryString from 'query-string';

import * as productService from '@/service/product-service';
import { ERROR_CODE, ORDER_PAY_STATUS } from '@/constants';
import { IOrderStatus } from '@/types/payment';
import styles from './result.less';
import useSimpleCountDown from '@/components/hooks/useSimpleCountDown';
import { tools } from '@/utils';
import useCheckWechatAuth from '@/components/hooks/useCheckWechatAuth';
import msToast from '@/components/modal/msToast';
import monitor from '@/utils/monitor';
import useWechatSdk from '@/components/hooks/useWechatSdk';
import { shareConfig } from '@/config';

import loadingImg from '@/public/images/common-loading.gif';
import EmptyPage from '@/views/components/empty-page';

const defaultShareOptions = shareConfig.default;

/**
 * url 
 */
interface IUrlParams {
  /**
   * 
   */
  orderNo: string;
}

interface IErrorState {
  isError: boolean;
  message: string | null;
}

/**
 * 
 */
const Result: FC<{}> = () => {
  const [loading, setLoading] = useState(true);
  const [poll, setPoll] = useState(false);
  const [orderStatus, setOrderStatus] = useState<IOrderStatus | null>(null);
  // 
  const [error, updateError] = useState<IErrorState>({ isError: false, message: null });

  const { orderNo } = queryString.parse(window.location.search) as IUrlParams;

  /**
   * 
   */
  useCheckWechatAuth({ support: 'all' });

  /**
   * JS-SDK, 
   */
  useWechatSdk({ shareStatus: true, shareOptions: defaultShareOptions });

  /**
   * 
   * countdownTime 
   * updateCountdownActive  
   */
  const [countdownTime, updateCountdownActive] = useSimpleCountDown(
    5,
    () => {
      // loading
      setLoading(false);
      // 
      setPoll(false);
    },
    false
  );

  /**
   * , 
   * orderStatus.parentOrderNo, 
   * , 
   * , 
   */
  const handleRedirect = () => {
    let url = tools.getFullPath(`/order`);
    if (orderStatus) {
      url = tools.getFullPath(`/order/${(orderStatus as IOrderStatus).parentOrderNo}`);
    }
    window.location.href = url;
  };

  /**
   * 
   * isInit: 
   */
  const queryOrderStatus = (isInit = false) => {
    productService
      .queryOrderStatus({
        orderNo
      })
      .then((orderStatus: IOrderStatus) => {
        // 
        setOrderStatus(orderStatus);

        const success = orderStatus.status === ORDER_PAY_STATUS.BIZ_ORDER_ALREADY_PAID;

        // 
        if (success) {
          // loading
          setLoading(false);
          // 
          setPoll(false);
        }

        // ,  '', 
        if (isInit && !success) {
          setPoll(true);
        }

        // 
        monitor.log({
          eventCode: tools.getEventCodeByPaymentPage(success),
          message: `: ${success ? '' : orderStatus.status}`
        });
      })
      .catch((error: any) => {
        const { code, msg } = error;

        // loading
        setLoading(false);
        // 
        setPoll(false);

        // 
        monitor.error({
          message: `: , ${JSON.stringify(error)}`
        });
        // 
        updateError({ isError: true, message: null });
        switch (code) {
          case ERROR_CODE.LOGIN_REQUIRED:
            msToast.modal({
              content: '',
              closeVisible: false,
              okText: '',
              onOk: () => {
                window.location.replace(tools.getLoginUrl());
              }
            });
            break;
          case ERROR_CODE.BIZ_ORDER_NOT_EXIST: // 
          case ERROR_CODE.BIZ_ORDER_IS_REFUND: // 
          case ERROR_CODE.BIZ_ORDER_EXPIRYTIME_OVER: // 
          case ERROR_CODE.BIZ_ORDER_IS_CLOSE: // 
          case ERROR_CODE.BIZ_ORDER_IS_SUB: // 
          case ERROR_CODE.BIZ_ORDER_NO_USER_ID_NOT_MATCH: // ID
            msToast.modal({
              content: msg || '',
              closeVisible: false,
              okText: '',
              onOk: () => {
                window.location.replace(tools.getFullPath('/order'));
              }
            });
            break;
          default:
            updateError({ isError: true, message: msg || '' });
            break;
        }
      });
  };

  /**
   * 
   */
  useEffect(() => {
    updateCountdownActive(true);
  }, []);

  /**
   *  
   */
  useEffect(() => {
    queryOrderStatus(true);
  }, []);

  /**
   * , , 
   */
  useEffect(() => {
    if (!poll) {
      return;
    }

    const timer = setInterval(() => {
      queryOrderStatus();
    }, 1000);

    return () => {
      clearInterval(timer);
    };
  }, [poll]);

  if (error.isError) {
    if (error.message) {
      return <EmptyPage describe={error.message} />;
    }
    return null;
  }

  let content = null;
  if (loading) {
    content = (
      <>
        <div className={styles.loading}>
          <img src={loadingImg} />
        </div>
        <p> {countdownTime}</p>
      </>
    );
  } else {
    // 
    let success = false;
    let statusText = '';
    // 
    let isContinue = false;

    if (orderStatus) {
      success = orderStatus.status === ORDER_PAY_STATUS.BIZ_ORDER_ALREADY_PAID ? true : false;
      statusText = orderStatus.statusText;
      isContinue = orderStatus.isContinue;
    }

    content = (
      <>
        <div className={success ? styles.success : styles.fail}>
          <div />
          <p>{success ? '' : statusText}</p>
        </div>
        <button onClick={handleRedirect}>{isContinue ? '' : ''}</button>
      </>
    );
  }

  return (
    <div className={styles.result}>
      <div className={styles.content}>{content}</div>
    </div>
  );
};

export default Result;
