import { useState, useRef } from 'react';
import useInterval from '@/components/hooks/useInterval';
import { queryOrderStatus } from '@/service/product-service';
import { ORDER_PAY_STATUS } from '@/constants';
import { IOrderStatus } from '@/types/payment';
import message from '@/utils/message';
import { getLoginUrl } from '@/utils/tools';
import monitor from '@/utils/monitor';
import msToast from '@/components/modal/msToast';
import { tools } from '@/utils';

/**
 * state
 */
interface IPollingOrderState {
  active: boolean;
  payResultUrl: string; // 
}

/**
 * 
 */
const POLLING_TIME = 4000;

/**
 * 
 * @param orderNo 
 */
const usePollingOrder = (orderNo?: string) => {
  const [{ active, payResultUrl }, updatePollingState] = useState<IPollingOrderState>({
    active: false,
    payResultUrl: ''
  });

  /**
   * 
   */
  const ref = useRef<number>(0);

  useInterval(async () => {
    if (!orderNo || !payResultUrl) {
      updatePollingState((state) => ({
        ...state,
        active: false
      }));
      return;
    }
    if (ref.current > 3) {
      // 4
      updatePollingState((state) => ({
        ...state,
        active: false
      }));
      message.closeLoading();
      msToast.modal({
        content: '',
        closeVisible: false,
        okText: '',
        onOk: () => {
          window.location.replace(tools.getFullPath('/order'));
        }
      });
      monitor.log({
        message: `[API]${ref.current}`
      });
      return;
    }
    try {
      // 
      ref.current = ref.current + 1;
      monitor.log({
        message: `[API]${ref.current} => ${JSON.stringify({
          orderNo
        })}`
      });

      const orderStatus: IOrderStatus = await queryOrderStatus({ orderNo });

      monitor.log({
        message: `[API]${ref.current} => ${JSON.stringify(orderStatus)}`
      });

      switch (orderStatus.status) {
        /**
         * 
         */
        case ORDER_PAY_STATUS.BIZ_ORDER_NOT_PAID:
          console.log(', ...');
          break;
        /**
         * 
         */
        case ORDER_PAY_STATUS.BIZ_ORDER_ALREADY_PAID:
          // 
          updatePollingState((state) => ({
            ...state,
            active: false
          }));
          // url
          window.location.replace(payResultUrl);
          break;
        /**
         * 
         */
        case ORDER_PAY_STATUS.LOGIN_REQUIRED:
          updatePollingState((state) => ({
            ...state,
            active: false
          }));
          message.error('', () => {
            window.location.replace(getLoginUrl());
          });
          break;
        /**
         * 
         */
        default:
          updatePollingState((state) => ({
            ...state,
            active: false
          }));
          message.error(orderStatus.statusText || '');
          break;
      }
    } catch (error) {
      monitor.error({
        message: `[API]${ref.current} => ${JSON.stringify(error)}`
      });
      // 
      updatePollingState((state) => ({
        ...state,
        active: false
      }));
      const errorMessage = (error && error.msg) || ', ';
      message.error(errorMessage);
    }
  }, (active && POLLING_TIME) || null);

  return updatePollingState;
};

export default usePollingOrder;
