import React, { FC, useEffect } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import Monitor from '@mmears/monitor';

import * as tools from '@/utils/tools';
import ErrorBoundary from '@/components/error-boundary';
import router from './router';

import { appConfig } from './config';

// 
const monitor = new Monitor();

const App: FC = () => {
  /**
   * eruda
   */
  useEffect(() => {
    /**
     * , 
     */
    if (appConfig.env !== 'production' && tools.isWap()) {
      const script = document.createElement('script');
      script.src = '//cdn.bootcss.com/eruda/1.5.7/eruda.min.js';
      document.body.appendChild(script);
      script.onload = () => {
        (window as any).eruda && (window as any).eruda.init();
      };
    }

    // 
    monitor.init();

    return () => {
      // 
      monitor.destroy();
    };
  }, []);

  //  
  const basename = appConfig.basename;

  return (
    <ErrorBoundary>
      <Router basename={basename}>{router}</Router>
    </ErrorBoundary>
  );
};

export default App;
